// stylelint-disable selector-no-qualifying-type
// stylelint-disable declaration-no-important

.fade {
  @include transition($transition-fade);

  &:not(.show) {
    opacity: 0;
  }
}

.collapse {
  &:not(.show) {
    display: none;
  }
}

.collapsing {
  position: relative;
  height: 0;
  overflow: hidden;
  @include transition($transition-collapse);
}


//
// Special effects
//

@if ($enable-transitions) {
  // Section background animation
  .section[data-animation="sectionHighlight"] {
    &.js-animation-item-animate {
      @include css-variable(background-color, $white, --color-white);

      @include dark-mode(&) {
        @include css-variable(background-color, $black, --color-black, !important);
      }
    }
  }

  // Text animations
  div[data-animation="textAnimation"],
  section[data-animation="textAnimation"] {
    overflow: hidden;
    
    // Preparation
    .text-animation-item {
      opacity: 0;
      transition: $transition-text-animation;
      transform: translateY(60px);
      will-change: opacity, transform;
    }

    // Animation
    .js-text-animation-item-animate {
      opacity: 1 !important;
      transform: translateY(0);
    }

    // Closing
    .js-remove-changes {
      will-change: auto;
      transition: none;
    }
  }
}
