//
// Toasts
// ------------------------

.toast {
  @if $enable-css-variables {
    --toast-bg: #{$toast-background-color};
    --toast-header-bg: #{$toast-header-background-color};
    --toast-color: #{$toast-color};
    --toast-header-color: #{$toast-header-color};
  }

  @include dark-mode(&) {
    --toast-bg: #{$toast-background-color-dark};
    --toast-header-bg: #{$toast-header-background-color-dark};
    --toast-color: #{$white};
    --toast-header-color: #{$toast-header-color-dark};
  }

  position: fixed;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: $zindex-toast;
  max-width: 100%;
  overflow: hidden; // cheap rounded corners on nested items
  font-size: $toast-font-size;
  background-color: $toast-background-color;
  background-clip: padding-box;
  border: none;
  border-radius: 0;
  box-shadow: $toast-box-shadow;
  opacity: 0;
  transform: translate(0, 200px);
  @include css-variable(color, $toast-color, --toast-color);
  @include css-variable(background-color, $toast-background-color, --toast-bg);
  @include transition(all .75s cubic-bezier(.165, .84, .44, 1));

  @supports (display: grid) {
    backdrop-filter: blur(10px);
  }

  @include media-breakpoint-up(md) {
    right: auto;
    bottom: $toast-position-y;
    left: $toast-position-x;
    max-width: $toast-max-width;
    border: $toast-border-width solid $toast-border-color;
    @include border-radius($toast-border-radius);
  }

  &:not(:last-child) {
    margin-bottom: $toast-padding-x;
  }

  &.showing {
    opacity: 1;
    transform: translate(0, 0);
  }

  &.show {
    display: block;
    opacity: 1;
    transform: translate(0, 0);
  }

  &.hide {
    display: none;
    transform: translate(0, 200px);
  }
}

.toast-header {
  display: flex;
  align-items: center;
  padding: $toast-padding-y $toast-padding-x;
  background-clip: padding-box;
  border-bottom: $toast-border-width solid $toast-header-border-color;
  @include css-variable(color, $toast-header-color, --toast-header-color);
  @include css-variable(background-color, $toast-header-background-color, --toast-header-bg);
}

.toast-body {
  padding: $toast-padding-x; // apply to both vertical and horizontal
}
