//
// Backdrop
//

.backdrop-blur-sm,
.backdrop-blur,
.backdrop-blur-lg {
  background: rgba(0, 0, 0, .5);
  box-shadow: $box-shadow-sm;

  @include border-radius($border-radius);
}

.br-safari .backdrop-blur-sm,
.br-safari .backdrop-blur,
.br-safari .backdrop-blur-lg,
.br-edge .backdrop-blur-sm,
.br-edge .backdrop-blur,
.br-edge .backdrop-blur-lg {
  background: rgba(0, 0, 0, .2);
}

.backdrop-blur-sm {
  backdrop-filter: blur(10px);
}
.backdrop-blur {
  backdrop-filter: blur(20px);
}
.backdrop-blur-lg {
  backdrop-filter: blur(30px);
}
