// stylelint-disable selector-no-qualifying-type

//
// Flat input kit
// ---------------------------

@if $enable-forms-flat {

  //
  // Textual form controls
  //

  .form-control-flat {
    @if $enable-css-variables {
      --input-border: #{$input-border-color};
      --input-color: #{$input-color};
    }

    @include dark-mode(&) {
      --input-border: #{$gray-600};
      --input-color: #{$white};
    }

    position: relative;
    margin-top: 1rem;
    border-bottom-style: solid;
    border-bottom-width: 1px;

    @include css-variable(color, $input-color, --input-color);
    @include css-variable(border-color, $input-border-color, --input-border);

    input,
    textarea {
      display: block;
      width: 100%;
      padding: .375rem .3rem;
      font-size: $font-size-base;
      line-height: $input-line-height;
      color: $input-color;
      background: none;
      border: none;
      border-radius: 0;
      box-shadow: none;

      &:focus {
        outline: none;
      }

      &:focus ~ label {
        @include css-variable(color, $primary, --color-primary);
      }

      &:focus ~ label,
      &:not(:placeholder-shown) ~ label {
        top: -1rem;
        font-size: #{$font-size-base - .2rem};
      }

      &:placeholder {
        color: rgba(0, 0, 0, 0);
        opacity: 0;
      }

      // stylelint-disable declaration-no-important
      &:focus ~ .bar > .ripple.animate {
        left: 0 !important;
        width: 100%;

        @include transition(all .3s $transition-cubic);
      }
      // stylelint-enable declaration-no-important

      &:focus ~ .highlight {
        animation: inputHighlighter .4s $transition-cubic both;
      }
    }

    input[type="password"] {
      letter-spacing: .3em;
    }

    label {
      position: absolute;
      top: .375rem;
      left: .3rem;
      font-size: $font-size-base;
      font-weight: $font-weight-normal;
      color: $input-color;
      pointer-events: none;
      @include transition(all .3s $transition-cubic);
    }

    .highlight {
      position: absolute;
      top: .375rem;
      left: .3rem;
      width: 0;
      height: #{$font-size-base + .5rem};
      pointer-events: none;
      opacity: .3;
      @include css-variable(background, $primary, --color-primary);
    }
  }


  //
  // Form groups
  //

  .form-group-flat {
    @if $enable-css-variables {
      --input-border: #{$input-border-color};
      --input-color: #{$input-color};
    }

    @include dark-mode(&) {
      --input-border: #{$gray-600};
      --input-color: #{$white};
    }

    margin-bottom: $form-group-margin-bottom;

    > .input-group {
      .input-group-append,
      .input-group-prepend {
        align-items: flex-end;

        .btn {
          border-color: transparent;
          border-width: 0;

          border-bottom-style: solid;
          border-bottom-width: 1px;
          border-radius: 0;

          @include css-variable(border-bottom-color, $input-border-color, --input-border);
        }
      }
    }
  }


  //
  // Input groups
  //

  .input-group-text.flat {
    @if $enable-css-variables {
      --input-border: #{$input-border-color};
      --input-color: #{$input-color};
    }

    @include dark-mode(&) {
      --input-border: #{$gray-600};
      --input-color: #{$white};
    }

    align-items: flex-end;
    padding-bottom: .375rem;
    background-color: transparent;
    border: none;
    border-bottom-style: solid;
    border-bottom-width: 1px;
    border-radius: 0;

    @include css-variable(border-color, $input-border-color, --input-border);
  }


  //
  // Checkboxes and radios
  //

  .form-check-flat {
    position: relative;
    display: block;
    user-select: none;

    .form-check-ripple {
      position: absolute;
      top: -.15rem;
      left: -.6rem;
      z-index: -1;
      display: block;
      margin: 0;
      pointer-events: none;
      background-color: $form-check-border-color;
      outline: none;
      box-shadow: none;
      opacity: 0;
      transform: scale(1);

      @include square(2.5rem);
      @include border-radius(50%);
      @include transition(opacity .3s $transition-cubic, transform .2s $transition-cubic);
    }
  
    // Checkbox
    .form-check-input[type="checkbox"] {
      position: absolute;
      left: -9999px;
      z-index: -1;
      appearance: none;
  
      & + label,
      & + .form-check-label {
        position: relative;
        padding: 1px 0;
        padding-left: $form-check-input-gutter;
        line-height: 2rem;
        cursor: pointer;
        @include transition(color .3s $transition-cubic);
            
        &::before {
          position: absolute;
          top: .45rem;
          left: 0;
          box-sizing: border-box;
          display: inline-block;
          vertical-align: top;
          content: "";
          border: solid 2px; /* Safari */
          border-color: $form-check-border-color;

          @include border-radius($form-check-border-radius);
          @include square(1.32rem);
          @include transition(border-color .3s, background-color .3s);
        }
      
        &::after {
          position: absolute;
          top: .65rem;
          left: .2rem;
          display: block;
          width: .7rem;
          height: .4rem;
          content: "";
          border: solid 2px transparent;
          border-top: none;
          border-right: none;
          transform: translate(.1rem, .2rem) rotate(-45deg);
        }
      }

      &:checked + label + .form-check-ripple,
      &:indeterminate + label + .form-check-ripple {
        @include css-variable(background-color, $primary, --color-primary);
      }
    
      &:checked + label {
        &::before {
          @include css-variable(background-color, $primary, --color-primary);
          @include css-variable(border-color, $primary, --color-primary);
        }

        &::after {
          border-color: $white;
        }
      
      }

      &:indeterminate + label {
        &::after {
          border-left: none;
          transform: translate(.2rem, .1rem);
        }
      }
    
      &:disabled {
        & + label {
          pointer-events: none;
          @include opacity(50);
        }
      
        & + label::before {
          pointer-events: none;
          @include opacity(50);
        }
      
        &:checked + label::after {
          pointer-events: none;
          @include opacity(50);
        }
      }
    }

    &:hover > .form-check-input[type="checkbox"]:not(:disabled):not(:checked) + label::before {
      border-color: $input-color;
    }

    & > .form-check-input[type="checkbox"]:checked:active + label::before {
      border-color: transparent;

      @include css-variable(background-color, $primary, --color-primary);
    }

    // Radio
    .form-check-input[type="radio"] {
      position: absolute;
      left: -9999px;
      z-index: -1;
      appearance: none;

      & + label,
      & + .form-check-label {
        position: relative;
        padding: 1px 0;
        padding-left: 2rem;
        line-height: 2rem;
        cursor: pointer;
        @include transition(color .3s $transition-cubic);
            
        &::before {
          position: absolute;
          top: .45rem;
          left: 0;
          z-index: 1;
          cursor: pointer;
          content: "";
          border: 2px solid $form-check-border-color;
        
          @include square(1.32rem);
          @include border-radius(50%);
          @include transition(border .3s $transition-cubic);
        }

        &::after {
          position: absolute;
          top: .45rem;
          left: 0;
          cursor: pointer;
          content: "";
          
          transform: scale(0);
          @include square(1.32rem);
          @include border-radius(50%);
          @include transition(all .3s $transition-cubic);
        }
      }

      &:checked + label + .form-check-ripple {
        @include css-variable(background-color, $primary, --color-primary);
      }

      &:checked + label,
      &:checked + .form-check-label {
        &::before {
          @include css-variable(border-color, $primary, --color-primary);
        }

        &::after {
          transform: scale(.6);
          @include css-variable(background-color, $primary, --color-primary);
        }
      }

      &:hover + label::before {
        border-color: $input-color;
      }
    
      &:hover + label:hover .radio-body {
        @include css-variable(border-color, $primary, --color-primary);
      }

      &:disabled {
        & + label {
          pointer-events: none;
          @include opacity(50);
        }
      
        & + label::before {
          pointer-events: none;
          @include opacity(50);
        }
      
        &:checked + label::after {
          pointer-events: none;
          @include opacity(50);
        }
      
      }

    }

    //Hover, Focus
    &:hover > .form-check-input:not(:disabled) + label + .form-check-ripple {
      opacity: .04;
    }

    & > .form-check-input:not(:disabled):focus + label + .form-check-ripple {
      opacity: .12;
    }

    &:hover > .form-check-input:not(:disabled):focus + label + .form-check-ripple {
      opacity: .16;
    }

    // Active
    & > .form-check-input:active {
      & + label::before {
        @include css-variable(border-color, $primary, --color-primary);
      }

      & + label + .form-check-ripple {
        transform: scale(0);

        @include opacity(100);
        @include transition(transform 0s, opacity 0s);
      }
    }
  }


  //
  // Switches
  //

  .form-switch-flat {
    position: relative;
    display: block;
    user-select: none;

    &.switch-io {
      .form-switch-flat-slider::after {
        position: absolute;
        top: 0;
        left: .4rem;
        z-index: 1;
        width: 2.25rem;
        height: 1rem;
        font-size: .5rem;
        line-height: 1rem;
        color: #fff;
        text-align: center;
        letter-spacing: 1rem;
        content: "IO";
      }
    }

    &:hover {
      .form-switch-flat-input:not(:disabled):not(:checked) + label .form-switch-flat-slider {
        background-color: $form-switch-hover-color;
      }
    }

    .form-switch-flat-input {
      position: absolute;
      left: -9999px;
      z-index: -1;
      appearance: none;
      
      &:checked + label .form-switch-flat-slider {
        @if $enable-css-variables {
          @supports ( (--a: 0)) {
            background-color: rgba(var(--rgb-primary), .4);
          }
        } @else {
          @include background-opacity($primary, .4);
        }
        
        &::before {
          transform: translate(1.25rem, 0);

          @include css-variable(background-color, $primary, --color-primary);
        }
      }

      &:checked + label .form-switch-ripple {
        transform: translate(1.25rem, 0);

        @include css-variable(background-color, $primary, --color-primary);
      }

      &:disabled {
        & ~ .form-switch-flat-label {
          pointer-events: none;
          @include opacity(50);
        }
      }
    }

    .form-switch-flat-slider {
      position: absolute;
      width: 2.25rem;
      height: 1rem;
      margin-top: $form-switch-input-margin-y + .2rem;
      margin-left: -$form-switch-input-gutter - .25rem;
      cursor: pointer;

      @include background-opacity($black,.2);
      @include dark-mode(&) {
        @include background-opacity($white,.2);
      }

      @include border-radius(.5rem);
      @include transition(all .3s $transition-cubic);
      
      &::before {
        position: absolute;
        bottom: -.25rem;
        left: -.25rem;
        z-index: 2;
        width: 1.5rem;
        height: 1.5rem;
        content: "";
        background-color: #fff;
      
        @include border-radius(50%);
        @include box-shadow(0 1px 3px 0 rgba($black, .21));
        @include transition(all .3s $transition-cubic);
      }
    }

    .form-switch-ripple {
      position: absolute;
      top: -.25rem;
      left: -.55rem;
      z-index: -1;
      display: block;
      margin: 0;
      pointer-events: none;
      background-color: $form-check-border-color;
      outline: none;
      box-shadow: none;
      opacity: 0;
      transform: scale(1);

      @include square(2.5rem);
      @include border-radius(50%);
      @include transition(opacity .3s $transition-cubic, transform .3s $transition-cubic);
    }

    .form-switch-flat-label {
      padding-left: $form-switch-input-gutter + .5rem;
      margin-bottom: .5rem;
      line-height: 2rem;
      vertical-align: top;
      cursor: pointer;
    }

    //Hover, Focus
    &:hover > .form-switch-flat-input:not(:disabled) + label .form-switch-ripple {
      opacity: .04;
    }

    & > .form-switch-flat-input:not(:disabled):focus + label .form-switch-ripple {
      opacity: .12;
    }

    &:hover > .form-switch-flat-input:not(:disabled):focus + label .form-switch-ripple {
      opacity: .16;
    }

    // Active
    & > .form-switch-flat-input:active {
      & + label::before {
        @include css-variable(border-color, $primary, --color-primary);
      }

      & + label .form-switch-ripple {
        transform: scale(0);

        @include opacity(100);
        //@include transition(transform 0s, opacity 0s);
      }
    }
  }


  //
  // Range
  //

  .form-range-flat {
    position: relative;
    display: flex;
    justify-content: center;

    input[type="range"].form-range-flat-input {
      width: $form-range-track-width;
      padding: 0 6px;
      margin: $form-range-thumb-height/2 0;
      background: transparent;
      appearance: none;

      &:focus {
        outline: none;
      }

      &::-webkit-slider-runnable-track {
        @include range-flat-track();
        background: $form-range-track-color;
        border: $form-range-track-border-width solid $form-range-track-border-color;

        @include border-radius($form-range-track-radius);
      }
    
      &::-webkit-slider-thumb {
        @include range-flat-thumb();
        margin-top: ((-$form-range-track-border-width * 2 + $form-range-track-height) / 2) - ($form-range-thumb-height / 2);
        appearance: none;
      }

      &:focus::-webkit-slider-runnable-track {
        background: lighten($form-range-track-color, $form-range-contrast);
      }

      &::-moz-range-track {
        @include range-flat-track();
        background: $form-range-track-color;
        border: $form-range-track-border-width solid $form-range-track-border-color;

        @include border-radius($form-range-track-radius);
      }
      &::-moz-range-thumb {
        @include range-flat-thumb();
      }

      &::-ms-track {
        @include range-flat-track();
        color: transparent;
        background: transparent;
        border-color: transparent;
        border-width: $form-range-thumb-width 0;
      }

      &::-ms-fill-lower {
        background: darken($form-range-track-color, $form-range-contrast);
        border: $form-range-track-border-width solid $form-range-track-border-color;

        @include border-radius($form-range-track-radius*2);
      }
      &::-ms-fill-upper {
        background: $form-range-track-color;
        border: $form-range-track-border-width solid $form-range-track-border-color;

        @include border-radius($form-range-track-radius*2);
      }
      &::-ms-thumb {
        @include range-flat-thumb();
      }
      &:focus::-ms-fill-lower {
        background: $form-range-track-color;
      }
      &:focus::-ms-fill-upper {
        background: lighten($form-range-track-color, $form-range-contrast);
      }
    }
  }


  //
  // File
  //

  .form-file-flat {
    position: relative;
    display: inline-block;
    width: 100%;
    height: $form-file-height;
    margin-top: 1rem;
    margin-bottom: 0;

    .form-file-flat-input {
      position: relative;
      z-index: 2;
      width: 100%;
      height: $form-file-height;
      margin: 0;
      cursor: pointer;
      opacity: 0;

      // &:focus ~ .form-file-label {
      // TODO
      // }

      // stylelint-disable selector-max-class, declaration-no-important
      &:focus ~ .bar > .ripple.animate {
        left: 0 !important;
        width: 100%;

        @include transition(all .3s $transition-cubic);
      }
      // stylelint-enable selector-max-class, declaration-no-important

      &:disabled ~ .form-file-label {
        background-color: $form-file-disabled-bg;
      }

      @each $lang, $value in $form-file-text {
        &:lang(#{$lang}) ~ .form-file-label::after {
          content: $value;
        }
      }
    }

    .form-file-flat-label {
      position: absolute;
      top: 0;
      right: 0;
      left: 0;
      z-index: 1;
      height: $form-file-height;
      padding: .375rem .3rem;
      line-height: $form-file-line-height;
      color: $form-file-color;
      cursor: pointer;
      background-color: transparent;
      border-bottom: $form-file-border-width solid $form-file-border-color;

      &::after {
        @if $enable-css-variables {
          --file-btn-color: #{$form-file-color};
        }

        @include dark-mode(&) {
          --file-btn-color: #{$white};
        }

        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        z-index: 3;
        display: block;
        height: $form-file-height-inner;
        padding: $form-file-padding-y $form-file-padding-x;
        line-height: $form-file-line-height;
        color: $form-file-button-color;
        content: "Browse";
        background-color: transparent;
        border-left: inherit;

        @include border-radius(0 $form-file-border-radius $form-file-border-radius 0);
        @include css-variable(color, $form-file-button-color, --file-btn-color);
      }
    }
  }


  //
  // File drag&drop
  //

  .form-file-flat-dd {
    @if $enable-css-variables {
      --input-bg: #{$gray-100};
      --input-border: #{$input-border-color};
    }

    @include dark-mode(&) {
      --input-bg: #{$gray-800};
      --input-border: #{$gray-700};
    }

    position: relative;
    display: block;
    width: 100%;
    min-width: 250px;
    padding: 10px;
    margin: 0;
    text-align: center;

    @include box-shadow($box-shadow);
    @include css-variable(background-color, $gray-100, --input-bg);
    @include border-radius($input-border-radius);

    .form-file-flat-dd-inner {
      position: relative;
      display: block;
      width: 100%;
      padding: 40px 0;
      margin: 0;
      margin-bottom: 10px;
      text-align: center;
      
      border-style: dashed;
      border-width: 2px;

      @include css-variable(border-color, $input-border-color, --input-border);
    }

    input {
      position: absolute;
      top: 0;
      left: 0;
      z-index: 1;
      width: 100%;
      height: 100%;

      @include opacity(0);
    }

    .btn {
      z-index: 2;
    }
  }


  //
  // Focusbar
  //

  .form-control-flat,
  .form-file-flat {
    .bar {
      position: relative;
      display: block;
      width: 100%;

      > .ripple {
        position: absolute;
        bottom: -1px;
        left: 50%;
        width: 0;
        height: 2px;
        content: "";
        @include css-variable(background, $primary, --color-primary);
        @include transition(all .1s $transition-cubic);
      }
    }
  }


  //
  // Animations
  //

  @keyframes inputHighlighter {
    from { width: 100px; }
    // stylelint-disable-next-line declaration-block-single-line-max-declarations
    to  { width: 0; background: transparent; }
  }

}
