@mixin background-opacity($color, $alpha: .3, $important:"") {
  background-color: $color unquote($important);
  background-color: rgba($color, $alpha) unquote($important);
}

@mixin text-opacity($color, $alpha: .87, $important:"") {
  $amount: 100-($alpha*100);
  @if ($color != "inherit" or $color != "initial") {
    color: $color;
  } @else {
    @if (lightness($color) > 50) {
      color: darken($color, $amount) unquote($important);
    } @else {
      color: lighten($color, $amount) unquote($important);
    }
    color: rgba($color, $alpha) unquote($important);
  }
}

@mixin opacity($alpha, $important:"") {
  filter: alpha(opacity=$alpha) unquote($important);
  opacity: $alpha /100 unquote($important);
}
