// Form control focus state
//
// Generate a customized focus state and for any input with the specified color,
// which defaults to the `$input-focus-border-color` variable.
//
// We highly encourage you to not customize the default value, but instead use
// this to tweak colors on an as-needed basis. This aesthetic change is based on
// WebKit's default styles, but applicable to a wider range of browsers. Its
// usability and accessibility should be taken into account with any change.
//
// Example usage: change the default blue border and shadow to white for better
// contrast against a dark gray background.

@mixin form-control-focus() {
  &:focus {
    //color: $input-focus-color;
    //background-color: $input-focus-bg;
    outline: 0;
    @include css-variable(border-color, $input-focus-border-color, --color-primary);

    // Avoid using mixin so we can pass custom focus shadow properly
    @if $enable-shadows {
      box-shadow: $input-box-shadow, $input-focus-box-shadow;

      @if $enable-css-variables {
        @supports ( (--a: 0)) {
          box-shadow: $input-box-shadow, 0 0 0 $input-btn-focus-width rgba(var(--rgb-primary), .25);
        }
      }
    } @else {
      box-shadow: $input-focus-box-shadow;
    }
  }
}

@mixin form-simple-focus() {
  @if $enable-shadows {
    box-shadow: $input-box-shadow, $input-focus-box-shadow;
  } @else {
    box-shadow: $input-focus-box-shadow;
  }
}

@mixin form-simple-active-focus() {
  @if $enable-shadows {
    box-shadow: $input-box-shadow, $input-focus-box-shadow;

    @if $enable-css-variables {
      @supports ( (--a: 0)) {
        box-shadow: $input-box-shadow, 0 0 0 $input-btn-focus-width rgba(var(--rgb-primary), .25);
      }
    }
  } @else {
    box-shadow: $input-focus-box-shadow;
  }
}


@mixin form-validation-state($state, $color) {
  .#{$state}-feedback {
    position: relative;
    z-index: -1;
    //display: none;
    width: 100%;
    margin-top: $form-feedback-margin-top;
    font-size: $form-feedback-font-size;
    color: $color;
    pointer-events: none;
    opacity: 0;
    transform: translateY(-1rem);

    @include transition(all .3s $transition-cubic);
  }

  .#{$state}-tooltip {
    position: absolute;
    top: 100%;
    z-index: 5;
    display: none;
    max-width: 100%; // Contain to parent when possible
    padding: .5rem;
    margin-top: .1rem;
    font-size: .875rem;
    line-height: 1;
    color: $white;
    background-color: rgba($color, .8);
    border-radius: .2rem;
  }

  .form-control {
    .was-validated &:#{$state},
    &.is-#{$state} {
      border-color: $color;

      &:focus {
        border-color: $color;
        box-shadow: 0 0 0 $input-focus-width rgba($color, .25);
      }

      ~ .#{$state}-feedback,
      ~ .#{$state}-tooltip {
        //display: block;
        opacity: 1;
        transform: translateY(0);
      }
    }
  }

  .form-control-file {
    .was-validated &:#{$state},
    &.is-#{$state} {
      ~ .#{$state}-feedback,
      ~ .#{$state}-tooltip {
        //display: block;
        opacity: 1;
        transform: translateY(0);
      }
    }
  }

  .form-check-input {
    ~ .#{$state}-feedback {
      margin-top: -.5rem;
    }
    
    .was-validated &:#{$state},
    &.is-#{$state} {
      ~ .form-check-label {
        color: $color;
      }

      ~ .#{$state}-feedback,
      ~ .#{$state}-tooltip {
        //display: block;
        opacity: 1;
        transform: translateY(0);
      }
    }
  }

  // custom file
  .form-file-input {
    .was-validated &:#{$state},
    &.is-#{$state} {
      ~ .form-file-label {
        border-color: $color;

        &::before { border-color: inherit; }
      }

      ~ .#{$state}-feedback,
      ~ .#{$state}-tooltip {
        display: block;
      }

      &:focus {
        ~ .form-file-label {
          box-shadow: 0 0 0 $input-focus-width rgba($color, .25);
        }
      }
    }
  }
}


// Simple range

@mixin range-track() {
  width: $form-range-track-width;
  height: $form-range-track-height;
  cursor: pointer;

  @include transition(all .2 $transition-cubic);
}

@mixin range-thumb() {
  width: $form-range-thumb-width;
  height: $form-range-thumb-height;
  cursor: pointer;
  background: $form-range-thumb-color;
  border: $form-range-thumb-border-width solid $form-range-thumb-border-color;

  @include border-radius($form-range-thumb-radius);
}


// Flat range

@mixin range-flat-track() {
  width: $form-range-flat-track-width;
  height: $form-range-flat-track-height;
  cursor: pointer;

  @include transition(all .2 $transition-cubic);
}

@mixin range-flat-thumb() {
  width: $form-range-thumb-width;
  height: $form-range-thumb-height;
  cursor: pointer;
  
  @if $enable-shadows {
    @include box-shadow($box-shadow-sm);
  } @else {
    border: $form-range-thumb-border-width solid $form-range-thumb-border-color;
  }

  @include css-variable(background-color, $primary, --color-primary);
  @include border-radius($form-range-thumb-radius);
}
