@mixin alert-variant($background, $border, $color, $level, $var) {
  $level: abs($level);
  
  @if $enable-css-variables {
    --alert-color: #{$color};
  }

  @include dark-mode(&) {
    --alert-color: #{$white};
  }

  background-color: rgba($color, ($level * $theme-color-interval) / 100%);
  border-color: $border;
  @include css-variable(color, $color, --alert-color);

  @if $enable-css-variables {
    @supports ( (--a: 0)) {
      background-color: rgba(var(--rgb-#{$var}), ($level * $theme-color-interval) / 100%);
    }
  }

  hr {
    border-top-color: darken($border, 5%);
  }

  .alert-link {
    color: darken($color, 10%);
  }
}
