// Navbar
//
// Provide a static navbar from which we expand to create full-width, fixed, and
// other navbar variations.

.navbar {
  position: relative;
  display: flex;
  flex-wrap: wrap; // allow us to do the line break for collapsing content
  align-items: center;
  justify-content: space-between; // space out brand from logo
  padding: $navbar-padding-y $navbar-padding-x;

  @include safe-area();
  
  // Because flex properties aren't inherited, we need to redeclare these first
  // few properities so that content nested within behave properly.
  > .container,
  > .container-fluid {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    width: 100%;
  }

  // Horizontal scroll
  &.navbar-scrollable {
    flex-wrap: nowrap;
    padding: .5rem 0;

    > .nav {
      flex-wrap: nowrap;
      width: 100%;

      .nav-link {
        white-space: nowrap;
      }

      @include media-breakpoint-down(md) {
        .dropdown-toggle::after {
          display: none;
        }

        .dropdown-menu {
          display: none;
        }
      }
    }

    @include media-breakpoint-up(md) {
      &.justify-content-left ul {
        justify-content: left;
      }

      &.justify-content-center ul {
        justify-content: center;
      }

      &.justify-content-right ul {
        justify-content: right;
      }
    }
  }

  @include media-breakpoint-down(md) {
    &.navbar-scrollable {
      overflow-x: auto;
      overflow-y: visible;
    }
  }

  // Theme variables
  &.navbar-light {
    --navbar-light-color: #{$navbar-light-color};
    --navbar-light-hover-color: #{$navbar-light-hover-color};
    --navbar-light-active-color: #{$navbar-light-active-color};
    --navbar-light-disabled-color: #{$navbar-light-disabled-color};
    --navbar-light-toggler-icon-bg: #{$navbar-light-toggler-icon-bg};
    --navbar-light-toggler-border-color: #{$navbar-light-toggler-border-color};
    --navbar-sidebar-light-bg: #{$navbar-sidebar-light-bg};
    --navbar-sidebar-dark-bg: #{$navbar-sidebar-dark-bg};


    @include dark-mode(&) {
      --navbar-light-color: #{$navbar-dark-color};
      --navbar-light-hover-color: #{$navbar-dark-hover-color};
      --navbar-light-active-color: #{$navbar-dark-active-color};
      --navbar-light-disabled-color: #{$navbar-dark-disabled-color};
      --navbar-light-toggler-icon-bg: #{$navbar-dark-toggler-icon-bg};
      --navbar-light-toggler-border-color: #{$navbar-dark-toggler-border-color};
      --navbar-sidebar-light-bg: #{$navbar-sidebar-dark-bg};
    }
  }

  &.navbar-dark {
    --navbar-dark-color: #{$navbar-dark-color};
    --navbar-dark-hover-color: #{$navbar-dark-hover-color};
    --navbar-dark-active-color: #{$navbar-dark-active-color};
    --navbar-dark-disabled-color: #{$navbar-dark-disabled-color};
    --navbar-dark-toggler-icon-bg: #{$navbar-dark-toggler-icon-bg};
    --navbar-dark-toggler-border-color: #{$navbar-dark-toggler-border-color};
    --navbar-sidebar-light-bg: #{$navbar-sidebar-light-bg};
    --navbar-sidebar-dark-bg: #{$navbar-sidebar-dark-bg};

    @include dark-mode(&) {
      --navbar-light-color: #{$navbar-dark-color};
      --navbar-light-hover-color: #{$navbar-dark-hover-color};
      --navbar-light-active-color: #{$navbar-dark-active-color};
      --navbar-light-disabled-color: #{$navbar-dark-disabled-color};
      --navbar-light-toggler-icon-bg: #{$navbar-dark-toggler-icon-bg};
      --navbar-light-toggler-border-color: #{$navbar-dark-toggler-border-color};
      --navbar-sidebar-light-bg: #{$navbar-sidebar-dark-bg};
    }
  }
}


// Navbar brand
//
// Used for brand, project, or site names.

.navbar-brand {
  display: inline-block;
  padding-top: $navbar-brand-padding-y;
  padding-bottom: $navbar-brand-padding-y;
  margin-right: $navbar-padding-x;
  font-size: $navbar-brand-font-size;
  line-height: inherit;
  white-space: nowrap;

  @include hover-focus {
    text-decoration: none;
  }
}


// Navbar nav
//
// Custom navbar navigation (doesn't require `.nav`, but does make use of `.nav-link`).

.navbar-nav {
  display: flex;
  flex-direction: column; // cannot use `inherit` to get the `.navbar`s value
  padding-left: 0;
  margin-bottom: 0;
  list-style: none;

  .nav-link {
    padding-right: 0;
    padding-left: 0;
  }

  .dropdown-menu {
    position: static;
    float: none;
  }
}


// Navbar text
//
//

.navbar-text {
  display: inline-block;
  padding-top: $nav-link-padding-y;
  padding-bottom: $nav-link-padding-y;
}


// Responsive navbar
//
// Custom styles for responsive collapsing and toggling of navbar contents.

// When collapsed, prevent the toggleable navbar contents from appearing in
// the default flexbox row orienation. Requires the use of `flex-wrap: wrap`
// on the `.navbar` parent.
.navbar-collapse {
  flex-basis: 100%;
  flex-grow: 1;
  // For always expanded or extra full navbars, ensure content aligns itself
  // properly vertically. Can be easily overridden with flex utilities.
  align-items: center;

  .navbar-overlay {
    display: none;
  }

  @include media-breakpoint-down(md) {
    &.navbar-sidebar {
      position: fixed;
      top: 0;
      bottom: 0;
      z-index: $zindex-sidebar;
      display: flex;
      flex-direction: column;
      width: $navbar-sidebar-width;
      height: 100%;
      padding-top: $navbar-sidebar-header-height;
      overflow: hidden;
      overflow-y: auto;
      -webkit-overflow-scrolling: touch;
      
      @include css-variable(background-color, $navbar-sidebar-dark-bg, --navbar-sidebar-light-bg);
      @include box-shadow($box-shadow);
      @include transition(padding .4s $transition-cubic,background-color .4s $transition-cubic,box-shadow 200ms ease-in-out,transform .5s $transition-cubic);
      
      &::before {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: $navbar-sidebar-header-height;
        content: "";
        background: $navbar-sidebar-header-bg;
      }

      &.sidebar-left {
        left: 0;
        transform: translateX(calc(-100% - 20px));

        &.animate-in {
          transform: translateX(0);
        }
      }

      &.sidebar-right {
        right: 0;
        transform: translateX(calc(100% + 20px));

        &.animate-in {
          transform: translateX(0);
        }
      }

      & ~ .navbar-overlay {
        position: fixed;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        z-index: ($zindex-sidebar - 1);
        display: block;
        pointer-events: none;
        background-color: rgba($black, .5);
        opacity: 0;
        @include transition(opacity .4s $transition-cubic,background-color .4s $transition-cubic);

      }

      &.animate-in + .navbar-overlay {
        pointer-events: auto;
        opacity: 1;
      }

      .nav-item {
        padding: $navbar-toggler-padding-y $navbar-toggler-padding-x;
      }
    }
  }
}

// Button for toggling the navbar when in its collapsed state
.navbar-toggler {
  z-index: $zindex-sidebar + 10;
  padding: $navbar-toggler-padding-y $navbar-toggler-padding-x;
  margin-right: -#{$navbar-toggler-padding-x};
  font-size: $navbar-toggler-font-size;
  line-height: 1;
  background-color: transparent; // remove default button style
  border: $border-width solid transparent; // remove default button style
  @include border-radius($navbar-toggler-border-radius);

  @include hover-focus {
    text-decoration: none;
    outline: none;
  }

  // Opinionated: add "hand" cursor to non-disabled .navbar-toggler elements
  &:not(:disabled):not(.disabled) {
    cursor: pointer;
  }
}

// Toggle icons
.navbar-image-icon {
  display: inline-block;
  width: 1.5em;
  height: 1.5em;
  vertical-align: middle;
  content: "";
  background: no-repeat center center;
  background-size: 100% 100%;
}

.navbar-html-icon {
  position: relative;
  display: inline-block;
  width: 1.5em;
  height: 1.5em;
  vertical-align: middle;

  .sandwich-stripe {
    position: absolute;
    left: 3px;
    width: 24px;
    height: 2px;
  
    @include transition(all .5s $transition-cubic);
    
    &[data-idx="1"] {
      top: 9px;
    }
    &[data-idx="2"] {
      top: 15px;
    }
    &[data-idx="3"] {
      top: 21px;
    }
  }
}

.navbar-toggler.animate-in {
  .navbar-html-icon {
    .sandwich-stripe[data-idx="1"] {
      animation: stripe-1 .4s $transition-cubic both;
    }
    
    .sandwich-stripe[data-idx="2"] {
      animation: stripe-2 .2s $transition-cubic both;
    }
    
    .sandwich-stripe[data-idx="3"] {
      animation: stripe-3 .4s $transition-cubic both;
    }
  }
}

.navbar-toggler.animate-reverse {
  .navbar-html-icon {
    .sandwich-stripe[data-idx="1"] {
      animation: stripe-1-reverse .4s $transition-cubic both;
    }
    .sandwich-stripe[data-idx="2"] {
      animation: stripe-2-reverse .2s $transition-cubic .1s both;
    }
    .sandwich-stripe[data-idx="3"] {
      animation: stripe-3-reverse .4s $transition-cubic both;
    }
  }
}

.container > .navbar-toggler.animate-in {
  &.toggler-open-left {
    position: fixed;
    top: 6px;
    left: 10px;
  }

  &.toggler-open-right {
    position: fixed;
    top: 6px;
    right: 10px;
  }
}


// Generate series of `.navbar-expand-*` responsive classes for configuring
// where your navbar collapses.
.navbar-expand {
  @each $breakpoint in map-keys($grid-breakpoints) {
    $next: breakpoint-next($breakpoint, $grid-breakpoints);
    $infix: breakpoint-infix($next, $grid-breakpoints);

    &#{$infix} {
      @include media-breakpoint-down($breakpoint) {
        > .container,
        > .container-fluid {
          padding-right: 0;
          padding-left: 0;
        }
      }

      @include media-breakpoint-up($next) {
        flex-flow: row nowrap;
        justify-content: flex-start;

        .navbar-nav {
          flex-direction: row;

          .dropdown-menu {
            position: absolute;
          }

          .nav-link {
            padding-right: $navbar-nav-link-padding-x;
            padding-left: $navbar-nav-link-padding-x;
          }
        }

        // For nesting containers, have to redeclare for alignment purposes
        > .container,
        > .container-fluid {
          flex-wrap: nowrap;
        }

        .navbar-collapse {
          display: flex !important;  // stylelint-disable-line declaration-no-important

          // Changes flex-bases to auto because of an IE10 bug
          flex-basis: auto;
        }

        .navbar-toggler {
          display: none;
        }
      }
    }
  }
}


// Navbar themes
//
// Styles for switching between navbars with light or dark background.

// Dark links against a light background
.navbar-light {
  .navbar-brand {
    @include css-variable(color, $navbar-light-active-color, --navbar-light-active-color);

    @include hover-focus {
      @include css-variable(color, $navbar-light-active-color, --navbar-light-active-color);
    }
  }

  .navbar-nav {
    .nav-link {
      @include css-variable(color, $navbar-light-color, --navbar-light-color);

      @include hover-focus {
        @include css-variable(color, $navbar-light-hover-color, --navbar-light-hover-color);
      }

      &.disabled {
        @include css-variable(color, $navbar-light-disabled-color, --navbar-light-disabled-color);
      }
    }

    .show > .nav-link,
    .active > .nav-link,
    .nav-link.show,
    .nav-link.active {
      @include css-variable(color, $navbar-light-active-color, --navbar-light-active-color);
    }
  }

  .navbar-toggler {
    @include css-variable(color, $navbar-light-color, --navbar-light-color);
    @include css-variable(border-color, $navbar-light-toggler-border-color, --navbar-light-toggler-border-color);

    @include hover-focus {
      .navbar-html-icon > * {
        @include css-variable(background-color, $navbar-light-active-color, --navbar-light-active-color);
      }
    }
  }

  .navbar-image-icon {
    @include css-variable(background-image, $navbar-light-toggler-icon-bg, --navbar-light-toggler-icon-bg);
  }

  .navbar-html-icon > * {
    @include css-variable(background-color, $navbar-light-color, --navbar-light-color);
  }

  .navbar-text {
    @include css-variable(color, $navbar-light-color, --navbar-light-color);

    a {
      @include css-variable(color, $navbar-light-active-color, --navbar-light-active-color);

      @include hover-focus {
        @include css-variable(color, $navbar-light-active-color, --navbar-light-active-color);
      }
    }
  }
}

@include media-breakpoint-down(md) {
  .sidebar-light {
    @include css-variable(background-color, $navbar-sidebar-dark-bg, --navbar-sidebar-dark-bg);

    .navbar-nav {
      .nav-link {
        @include css-variable(color, $navbar-dark-color, --navbar-dark-color);

        @include hover-focus {
          @include css-variable(color, $navbar-dark-hover-color, --navbar-dark-hover-color);
        }

        &.disabled {
          @include css-variable(color, $navbar-dark-disabled-color, --navbar-dark-disabled-color);
        }
      }

      .show > .nav-link,
      .active > .nav-link,
      .nav-link.show,
      .nav-link.active {
        @include css-variable(color, $navbar-dark-active-color, --navbar-dark-active-color);
      }
    }
  }
}

// White links against a dark background
.navbar-dark {
