    }
  }
}


//
// Keyframes
//

// sandwich
@keyframes stripe-1 {
  0%   {
    top: 9px;
    transform: rotate(0deg);
  }
  100% {
    top: 15px;
    transform: rotate(45deg);
  }
}

@keyframes stripe-2 {
  0%   { opacity: 1; }
  100% { opacity: 0; }
}

@keyframes stripe-3 {
  0%   {
    top: 21px;
    transform: rotate(0deg);
  }
  100% {
    top: 15px;
    transform: rotate(-45deg);
  }
}

@keyframes stripe-1-reverse {
  0%   {
    top: 15px;
    transform: rotate(45deg);
  }
  100% {
    top: 9px;
    transform: rotate(0deg);
  }
}

@keyframes stripe-2-reverse {
  0%   { opacity: 0; }
  100% { opacity: 1; }
}

@keyframes stripe-3-reverse {
  0%   {
    top: 15px;
    transform: rotate(-45deg);
  }
  100% {
    top: 21px;
    transform: rotate(0deg);
  }
}
