// stylelint-disable selector-no-qualifying-type

//
// Buttons
//

.btn {
  position: relative;
  display: inline-block;
  max-width: 100%;
  overflow: hidden;
  font-weight: $btn-font-weight;
  text-align: center;
  text-overflow: ellipsis;
  white-space: nowrap;
  vertical-align: middle;
  user-select: none;
  border: $btn-border-width solid transparent;
  @include button-size($btn-padding-y, $btn-padding-x, $font-size-base, $btn-line-height, $btn-border-radius);
  @include transition($btn-transition);

  &::before {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 0;
    width: 100%;
    height: 100%;
    content: "";

    @include transition(opacity .3s $transition-cubic);
    @include opacity(0);
  }

  // Share hover and focus styles
  @include hover-focus {
    text-decoration: none;
  }

  &:focus,
  &.focus {
    outline: 0;

    &.btn-raised {
      box-shadow: $btn-focus-box-shadow;
    }
  }

  // Disabled comes first so active can properly restyle
  &.disabled,
  &:disabled {
    opacity: $btn-disabled-opacity;
    @include box-shadow(none);
  }

  // Opinionated: add "hand" cursor to non-disabled .btn elements
  &:not(:disabled):not(.disabled) {
    cursor: pointer;
  }

  &:not(:disabled):not(.disabled):active,
  &:not(:disabled):not(.disabled).active {
    background-image: none;

    &.btn-raised {
      @include box-shadow($btn-active-box-shadow);

      &:focus {
        @include box-shadow($btn-focus-box-shadow, $btn-active-box-shadow);
      }
    }
  }
}

// Future-proof disabling of clicks on `<a>` elements
a.btn.disabled,
fieldset:disabled a.btn {
  pointer-events: none;
}


//
// Alternate buttons
//

@each $color, $value in $theme-colors {
  .btn-#{$color} {
    @include button-variant($value, $value, $color);
  }
}


@each $color, $value in $theme-colors {
  .btn-fill-#{$color} {
    @include button-fill-variant($value, $value, $color);
  }
}

@each $color, $value in $theme-colors {
  .btn-outline-#{$color} {
    @include button-outline-variant($value, $color);
  }
}

@each $brand, $value in $brands {
  .btn-brand-#{$brand} {
    @include button-fill-variant($value, $value, $brand);
  }
}


//
// Link buttons
//

// Make a button look and behave like a link
.btn-link {
  font-weight: $font-weight-normal;
  background-color: transparent;

  @include css-variable(color, $link-color, --color-primary);
  @include transition(color .4s cubic-bezier(.25, .8, .25, 1));

  &:hover {
    text-decoration: $link-hover-decoration;
    background-color: transparent;
    border-color: transparent;

    @include css-variable(color, $link-hover-color, --color-hover-primary);
  }

  &:focus,
  &.focus {
    text-decoration: $link-hover-decoration;
    border-color: transparent;
    box-shadow: none;
  }

  &:disabled,
  &.disabled {
    color: $btn-link-disabled-color;
    pointer-events: none;
  }

  .ripple-effect {
    display: none;
  }

  // No need for an active state here
}


//
// Button Sizes
//

.btn-lg {
  @include button-size($btn-padding-y-lg, $btn-padding-x-lg, $font-size-lg, $btn-line-height-lg, $btn-border-radius-lg);
}

.btn-sm {
  @include button-size($btn-padding-y-sm, $btn-padding-x-sm, $font-size-sm, $btn-line-height-sm, $btn-border-radius-sm);
}


//
// Block button
//

.btn-block {
  display: block;
  width: 100%;

  // Vertically space out multiple block buttons
  + .btn-block {
    margin-top: $btn-block-spacing-y;
  }
}

// Specificity overrides
input[type="submit"],
input[type="reset"],
input[type="button"] {
  &.btn-block {
    width: 100%;
  }
}


//
// Ripple effect
//

//Ripple
.ripple-effect{
  position: absolute;
  display: block;
  background: rgba($primary, .4);
  border-radius: 100%;
  transform: scale(0);

  &.animate {
    animation: ripple-animation .65s linear;
  }
}

@keyframes ripple-animation {
  100% {
    opacity: 0;
    transform: scale(2.5);
  }
}


//
// Scrolltop button
//
// DEPRECATED
// Will be removed in next version. Use .fab-btn instead.
//

.scrolltop-btn {
  position: fixed;
  right: 5%;
  bottom: 25px;
  z-index: 989;
  overflow: hidden;
  cursor: pointer;
  background-repeat: no-repeat;
  background-position: 50%;
  background-size: 45%;
  border-radius: 50%;
  transform: scale(0);
  @include css-variable(background-color, $primary, --color-primary);

  @include square(50px);
  @include border-radius(50%);
  @include box-shadow($box-shadow);
  @include opacity(0);
  @include transition(transform .4s cubic-bezier(.25, .8, .25, 1), background-color .4s cubic-bezier(.25, .8, .25, 1), opacity .4s cubic-bezier(.25, .8, .25, 1));
  
  &::before {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 0;
    width: 100%;
    height: 100%;
    content: "";

    @include css-variable(background-color, $white, --color-white);
    @include transition(opacity .3s $transition-cubic);
    @include opacity(0);
  }

  &.show {
    @include opacity(100);
    transform: scale(1);

    @include hover-focus {
      &::before {
        @include opacity(4);
      }
    }
  }
}


//
// FAB buttons
//

.fab-btn {
  position: fixed;
  z-index: 989;
  overflow: hidden;
  line-height: 50px;
  text-align: center;
  cursor: pointer;
  background-color: $primary;
  background-repeat: no-repeat;
  background-position: 50%;
  background-size: 45%;
  transform: scale(0);
  animation: fabLoad .4s cubic-bezier(.25, .8, .25, 1) 0s 1 both;

  @include square(50px);
  @include border-radius(50%);
  @include box-shadow($box-shadow);
  @include transition(transform .4s cubic-bezier(.25, .8, .25, 1), background-color .4s cubic-bezier(.25, .8, .25, 1), opacity .4s cubic-bezier(.25, .8, .25, 1));

  @include hover-focus {
    &::before {
      @include opacity(4);
    }
  }

  &::before {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 0;
    width: 100%;
    height: 100%;
    content: "";

    @include css-variable(background-color, $white, --color-white);
    @include transition(opacity .3s $transition-cubic);
    @include opacity(0);
  }

  &.top {
    top: $fab-gutter-y;
  }

  &.bottom {
    bottom: $fab-gutter-y;
  }

  &.left {
    left: $fab-gutter-x;
  }

  &.right {
    right: $fab-gutter-x;
  }

  @each $color, $value in $theme-colors {
    &.fab-#{$color} {
      @include css-variable(background-color, $value, --color-#{$color});
    }
  }
}

@keyframes fabLoad {
  0% {
    @include opacity(0);
    transform: scale(0);
  }
  100% {
    @include opacity(100);
    transform: scale(1);
  }
}

