//
// Carousel
// --------------------------------------------------

@if $enable-carousel {
  
  .carousel {
    position: relative;
  }

  .carousel-inner {
    position: relative;
    width: 100%;
    overflow: hidden;
  }

  .carousel-item {
    position: relative;
    display: none;
    align-items: center;
    width: 100%;
    backface-visibility: hidden;
    perspective: 1000px;
    @include transition($carousel-transition);

    &.carousel-item-scrim {
      &::before {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        z-index: 0;
        content: "";
        background-color: rgba($black, $scrim-opacity);
      }
    }
  }


  // Optional captions
  .carousel-caption {
    position: absolute;
    right: ((100% - $carousel-caption-width) / 2);
    bottom: 20px;
    left: ((100% - $carousel-caption-width) / 2);
    z-index: 10;
    padding-top: 20px;
    padding-bottom: 20px;
    color: $carousel-caption-color;
    text-align: center;
  }


  // Slick styles
  .slick-slider {
    position: relative;
    box-sizing: border-box;
    display: block;
    touch-action: pan-y;
    user-select: none;
    -webkit-touch-callout: none;
    -webkit-tap-highlight-color: transparent;

    * {
      outline: none;
    }
  }
  .slick-list {
    position: relative;
    display: block;
    padding: 0;
    margin: 0;
    overflow: hidden;

    &:focus {
      outline: none;
    }

    &.dragging {
      cursor: pointer;
      cursor: hand;
    }
  }
  .slick-slider .slick-track,
  .slick-slider .slick-list {
    transform: translate3d(0, 0, 0);
  }

  .slick-track {
    position: relative;
    top: 0;
    left: 0;
    display: block;

    &::before,
    &::after {
      display: table;
      content: "";
    }

    &::after {
      clear: both;
    }

    .slick-loading & {
      visibility: hidden;
    }
  }
  
  .slick-slide {
    display: none;
    float: left;
    height: 100%;

    [dir="rtl"] & {
      float: right;
    }
    img {
      display: block;
    }
    &.slick-loading img {
      display: none;
    }

    &.dragging img {
      pointer-events: none;
    }

    .slick-initialized & {
      display: block;
    }

    .slick-loading & {
      visibility: hidden;
    }

    .slick-vertical & {
      display: block;
      height: auto;
      border: 1px solid transparent;
    }
  }

  .slick-arrow.slick-hidden {
    display: none;
  }


  // Controls
  .slick-prev,
  .slick-next {
    position: absolute;
    top: 50%;
    z-index: 10;
    display: block;
    width: $carousel-control-width;
    height: $carousel-control-height;
    padding: 0;
    font-size: 0;
    line-height: 0;
    color: transparent;
    cursor: pointer;
    background: transparent;
    border: none;
    outline: none;

    @include opacity($carousel-control-opacity);
    @include transition($carousel-control-transition);
      
    &:hover {
      color: transparent;
      outline: none;
      @include opacity(100);
    }
    &::before {
      font-size: 35px;
      line-height: 35px;
      color: $carousel-control-color;
      -webkit-font-smoothing: antialiased;
      -moz-osx-font-smoothing: grayscale;
    }
  }

  .slick-prev {
    left: 10px;

    &::before {
      display: inline-block;
      width: $carousel-control-icon-width;
      height: $carousel-control-icon-width;
      content: "";
      background: $carousel-control-prev-icon-bg 50% / contain no-repeat;
    }
  }

  .slick-next {
    right: 10px;

    &::before {
      display: inline-block;
      width: $carousel-control-icon-width;
      height: $carousel-control-icon-width;
      content: "";
      background: $carousel-control-next-icon-bg 50% / contain no-repeat;
    }
  }

  .slick-dots {
    position: absolute;
    bottom: 10px;
    display: block;
    width: 100%;
    padding: 0;
    margin: 0;
    text-align: $carousel-indicator-align;
    list-style: none;

    li {
      position: relative;
      display: inline-block;
      width: $carousel-indicator-width;
      height: $carousel-indicator-height;
      padding: 0;
      margin: 0 $carousel-indicator-spacer;
      cursor: pointer;

      button {
        position: relative;
        display: block;
        width: $carousel-indicator-width;
        height: $carousel-indicator-height;
        padding: 0; // To avoid padding on Safari
        font-size: 0;
        line-height: 0;
        color: $carousel-indicator-active-bg;
        cursor: pointer;
        background: $carousel-indicator-active-bg;
        border: 0;
        outline: none;

        @include opacity($carousel-indicator-opacity);
        @include transition($carousel-indicator-transition);

        &:hover {
          @include opacity(100);
        }

        &::before {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: $carousel-indicator-height;
          content: "";
          transform: scale(0, 1);
          transform-origin: left;
          @include css-variable(background-color, $primary, --color-primary);
        }
      }
      &.slick-active button {
        @include opacity(100);

        &::before {
          animation: carousel-indicator-animation $carousel-indicator-animation-time linear both;
        }
      }
    }
  }

  @keyframes carousel-indicator-animation {
    0% {
      transform: scale(0, 1);
    }
    100% {
      transform: scale(1, 1);
    }
  }


  // Pause indicator animation on hover
  .carousel:hover .slick-dots li button::before {
    animation-play-state: paused;
  }
}
