.pagination {
  @if $enable-css-variables {
    --pagination-border: #{$pagination-border-color};
    --pagination-color: #{$pagination-color};
    --pagination-bg: #{$pagination-bg};
    --pagination-hover-border: #{$pagination-border-color};
    --pagination-hover-color: #{$pagination-color};
    --pagination-hover-bg: #{$pagination-bg};
    --pagination-disabled-border: #{$pagination-disabled-border-color};
    --pagination-disabled-color: #{$pagination-disabled-color};
    --pagination-disabled-bg: #{$pagination-disabled-bg};
  }

  @include dark-mode(&) {
    --pagination-border: #{$gray-600};
    --pagination-color: #{$white};
    --pagination-bg: #{$gray-900};
    --pagination-hover-border: #{$gray-600};
    --pagination-hover-color: #{$white};
    --pagination-hover-bg: #{$gray-800};
    --pagination-disabled-border: #{$gray-600};
    --pagination-disabled-color: #{$gray-600};
    --pagination-disabled-bg: #{$gray-900};
  }

  display: flex;
  @include list-unstyled();
  @include border-radius();
}

.page-link {
  position: relative;
  display: block;
  padding: $pagination-padding-y $pagination-padding-x;
  margin-left: -$pagination-border-width;
  line-height: $pagination-line-height;
  color: $pagination-color;
  background-color: $pagination-bg;
  border: $pagination-border-width solid $pagination-border-color;

  @include css-variable(border-color, $pagination-border-color, --pagination-border);
  @include css-variable(color, $pagination-color, --pagination-color);
  @include css-variable(background-color, $pagination-bg, --pagination-bg);
  @include transition(all .3s $transition-cubic);

  &:hover {
    z-index: 2;
    text-decoration: none;

    @include css-variable(border-color, $pagination-hover-border-color, --pagination-hover-border);
    @include css-variable(color, $pagination-hover-color, --pagination-hover-color);
    @include css-variable(background-color, $pagination-hover-bg, --pagination-hover-bg);
  }

  &:focus {
    z-index: 2;
    outline: $pagination-focus-outline;
    box-shadow: $pagination-focus-box-shadow;
  }

  // Opinionated: add "hand" cursor to non-disabled .page-link elements
  &:not(:disabled):not(.disabled) {
    cursor: pointer;
  }
}

.page-item {
  &:first-child {
    .page-link {
      margin-left: 0;
      @include css-variable(color, $primary, --color-primary);
      @include border-left-radius($border-radius);
    }
  }
  &:last-child {
    .page-link {
      @include css-variable(color, $primary, --color-primary);
      @include border-right-radius($border-radius);
    }
  }

  &.active .page-link {
    z-index: 1;
    color: $pagination-active-color;
    @include css-variable(background-color, $pagination-active-bg, --color-primary);
    @include css-variable(border-color, $pagination-active-border-color, --color-primary);
  }

  &.disabled .page-link {
    pointer-events: none;
    // Opinionated: remove the "hand" cursor set previously for .page-link
    cursor: auto;

    @include css-variable(border-color, $pagination-disabled-border-color, --pagination-disabled-border);
    @include css-variable(color, $pagination-disabled-color, --pagination-disabled-color);
    @include css-variable(background-color, $pagination-disabled-bg, --pagination-disabled-bg);
  }
}


//
// Sizing
//

.pagination-lg {
  @include pagination-size($pagination-padding-y-lg, $pagination-padding-x-lg, $font-size-lg, $line-height-lg, $border-radius-lg);
}

.pagination-sm {
  @include pagination-size($pagination-padding-y-sm, $pagination-padding-x-sm, $font-size-sm, $line-height-sm, $border-radius-sm);
}
