//
// Separators
//

// Double Diagonal separator
.separator-doublediagonal {
  position: relative;
  z-index: 10;
  overflow-x: visible;

  &::before,
  &::after {
    position: absolute;
    top: 0;
    left: -25%;
    z-index: -1;
    width: 150%;
    height: 75%;
    pointer-events: none;
    content: "";
    background: inherit;
    transform: rotate(-3deg);
    transform-origin: 0 0;
  }

  &::before {
    height: 50%;
    background-color: $primary;
    transform: rotate(-5deg);
    transform-origin: 3% 0;
  }
}


// Halfcircle separator
.separator-halfcircle {
  position: absolute;
  top: -50px;
  left: 50%;
  z-index: 10;
  width: 100px;
  height: 100px;
  cursor: pointer;
  background: inherit;
  border-radius: 50%;
  //transform: translate(-50%, 0)

  .down-arrow-icon {
    animation: separator-move-down 2s ease both infinite;
  }
}

