// Circle
// ------------------------

.separator-circle {
  position: absolute;
  top: -30px;
  left: 50%;
  z-index: 10;
  z-index: 15;
  width: 60px;
  height: 60px;
  margin: 0;
  cursor: pointer;
  background: $white;
  border-radius: 50%;
  
  //@include translate(-50%, 0);
  //@include box-shadow(0px 1px 3px 0px rgba($black, 0.21));

  .down-arrow-icon {
    width: 30px;
    height: 30px;
    margin: 15px;
    
    //@include transition(all .25s);
  }
}
