// stylelint-disable declaration-no-important
// Typography

@mixin text-emphasis-variant($parent, $color, $var) {
  #{$parent} {
    @include css-variable(color, $color, --color-#{$var}, !important);
  }
  a#{$parent} {
    @include hover-focus {
      color: darken($color, 10%) !important;
    }
  }
}
