//
// Layouts
// --------------------------------------------------

// Wrapper
.wrapper {
  position: relative;
  max-width: 100vw;
}

// The contain CSS property allows an author to indicate that an element
// and its contents are, as much as possible, independent of the rest of the document tree.
// This allows the browser to recalculate layout, style, paint, size,
// or any combination of them for a limited area of the DOM and not the entire page.
//
// https://developer.mozilla.org/en-US/docs/Web/CSS/contain

// .wrapper,
// .content,
// .nav {
//   contain: strict;
// }


// Content
.content {
  display: block;
  height: 100%;
  min-height: $content-height;
  padding-top: $content-offset-top;
}


// Container widths
//
// Set the container width, and override it for fixed navbars in media queries.
.container {
  position: relative;
  z-index: 3;
  height: 100%;
  padding-right: $container-gutter;
  padding-left: $container-gutter;
  margin: 0 auto;
  clear: both;

  @include safe-area();
  @include make-container-max-widths();
  @include transition(background-color .4s $transition-cubic);

  @include media-breakpoint-down(md) {
    & {
      padding-right: $container-gutter / 2;
      padding-left: $container-gutter / 2;
    }
  }
}


// Fluid container
//
// Utilizes the mixin meant for fixed width containers, but with 100% width for
// fluid, full width layouts.
@if $enable-grid-classes {
  .container-fluid {
    @include safe-area();
    @include make-container();
  }
}


// Sections
.section {
  position: relative;
  display: block;
  padding: $section-padding 0 $section-padding 0;
  // Causes scroll issues on Chrome
  // overflow-x: hidden;

  @include transition(background-color .4s $transition-cubic);

  .section-links {
    padding: 0;
    margin: 21px 0 0;
    text-align: center;
    list-style: none;
  }

  .section-link {
    display: inline-block;
    margin: 0 32px;
    cursor: pointer;
  }
}


// Landing
.landing {
  position: relative;
  height: $landing-height;
  min-height: $landing-height;
  padding: $landing-padding 0;
  // Causes scroll issues on Chrome
  // overflow-x: hidden;

  @include transition(background-color .4s $transition-cubic);

  /* @media screen and (max-device-width: 820px) and (min-device-width: 319px) and (orientation: landscape) {
    height: auto;
    min-height: 90vh;
  } */
  
  .landing-title {
    padding-bottom: 30px;
    word-spacing: .3px;
  }
  
  .landing-subtitle {
    padding-bottom: 40px;
    margin-top: -24px;
    word-spacing: .3px;
    
    &.hero-subtitle {
      margin: 0;
    }
  }
}


// Hero
.hero {
  position: relative;
  height: $hero-height;
  min-height: $hero-height;
  padding: $hero-padding 0;
  // Causes scroll issues on Chrome
  // overflow-x: hidden;

  @include transition(background-color .4s $transition-cubic);

  @media screen and (max-device-width: 820px) and (min-device-width: 319px) and (orientation: landscape) {
    height: auto;
    min-height: 100vh;
  }
  
  //Video
  .hero-video-bg-wrapper {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 0;
    overflow: hidden;
  }
  
  .hero-video-bg {
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: 0;
    width: auto;
    min-width: 100%;
    height: auto;
    min-height: 100%;
    margin: auto;
    visibility: visible;
    transform: translate(-50%, -50%);
  }
  
  .hero-title {
    margin: 0 0 14px;
    text-align: left;
    @if $hero-text-shadow {
      text-shadow: $text-shadow-base;
    }
  }

  .hero-subtitle {
    padding-bottom: 40px;
    margin-top: -24px;
    text-align: center;

    @if $hero-text-shadow {
      text-shadow: $text-shadow-base;
    }
  }

  .hero-description {
    margin-bottom: 16px;

    @if $hero-text-shadow {
      text-shadow: $text-shadow-base;
    }
  }
}


// Scrim
.scrim {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  width: 100%;
  height: 100%;

  @include background-opacity($scrim-color, $scrim-opacity);
}


// Vertical positioning
.vertical-center-outer {
  display: table;
  width: 100%;
  height: 100%;
}

.vertical-center-inner {
  display: table-cell;
  vertical-align: middle;

  @include make-container-max-widths();
}


// Independent background element
.self-bg {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
  width: calc(100% + 40px);
  height: 120%;
  overflow: hidden;
  transform: translate(-20px, -20px) scale(1.2);
}


// Parallax effect
.parallax-container {
  overflow: hidden;
}

//Scrollable area
.scrollabe-area {
  position: relative;
  height: $scrollarea-height;
  overflow: hidden;
  
  .scrollabe-area-wrap {
    position: relative;
    height: 100%;
    overflow-x: hidden;
    overflow-y: auto;

    -webkit-overflow-scrolling: touch;
    
    @if ($scrollarea-scrollbar == true) {
      padding-right: 30px;
      margin-right: -30px;
    }
    
    .scrollabe-area-body {
      width: $scrollarea-width;
      
      @include media-breakpoint-up(sm) {
        width: 100vw;
      }
    }
  }
}
