// stylelint-disable selector-no-qualifying-type

//
// Simple input kit
// ---------------------------

@if $enable-forms-simple {

  //
  // Textual form controls
  //

  .form-control {
    @if $enable-css-variables {
      --input-bg: #{$input-bg};
      --input-border: #{$input-border-color};
      --input-color: #{$input-color};
    }

    @include dark-mode(&) {
      --input-bg: #{$gray-900};
      --input-border: #{$gray-800};
      --input-color: #{$white};
    }

    display: block;
    width: 100%;
    padding: $input-padding-y $input-padding-x;
    font-size: $font-size-base;
    line-height: $input-line-height;
    background-clip: padding-box;
    border-style: solid;
    border-width: $input-border-width;

    @include css-variable(color, $input-color, --input-color);
    @include css-variable(background-color, $input-bg, --input-bg);
    @include css-variable(border-color, $input-border-color, --input-border);

    // Note: This has no effect on <select>s in some browsers, due to the limited stylability of `<select>`s in CSS.
    @if $enable-rounded {
      // Manually use the if/else instead of the mixin to account for iOS override
      border-radius: $input-border-radius;
    } @else {
      // Otherwise undo the iOS default
      border-radius: 0;
    }

    @include box-shadow($input-box-shadow);
    @include transition($input-transition);

    // Unstyle the caret on `<select>`s in IE10+.
    &::-ms-expand {
      background-color: transparent;
      border: 0;
    }

    // Customize the `:focus` state to imitate native WebKit styles.
    @include form-control-focus();

    // Placeholder
    &::placeholder {
      color: $input-placeholder-color;
      // Override Firefox's unusual default opacity; see https://github.com/twbs/bootstrap/pull/11526.
      opacity: 1;
    }

    // Disabled and read-only inputs
    //
    // HTML5 says that controls under a fieldset > legend:first-child won't be
    // disabled if the fieldset is disabled. Due to implementation difficulty, we
    // don't honor that edge case; we style them as disabled anyway.
    &:disabled,
    &[readonly] {
      background-color: $input-disabled-bg;
      // iOS fix for unreadable disabled content; see https://github.com/twbs/bootstrap/issues/11655.
      opacity: 1;
    }
  }

  select.form-control {
    &:not([size]):not([multiple]) {
      height: $input-height;
    }

    &:focus::-ms-value {
      // Suppress the nested default white text on blue background highlight given to
      // the selected option text when the (still closed) <select> receives focus
      // in IE and (under certain conditions) Edge, as it looks bad and cannot be made to
      // match the appearance of the native widget.
      color: $input-color;
      background-color: $input-bg;
    }
  }

  // Make file inputs better match text inputs by forcing them to new lines.
  .form-control-file,
  .form-control-range {
    display: block;
    width: 100%;
  }


  //
  // Readonly controls as plain text
  //
  // Apply class to a readonly input to make it appear like regular plain
  // text (without any border, background color, focus indicator)

  .form-control-plaintext {
    display: block;
    width: 100%;
    padding-top: $input-padding-y;
    padding-bottom: $input-padding-y;
    margin-bottom: 0; // match inputs if this class comes on inputs with default margins
    line-height: $input-line-height;
    color: $input-plaintext-color;
    background-color: transparent;
    border: solid transparent;
    border-width: $input-border-width 0;

    &.form-control-sm,
    &.form-control-lg {
      padding-right: 0;
      padding-left: 0;
    }
  }


  //
  // Checkboxes and radios
  //
  // Indent the labels to position radios/checkboxes as hanging controls.

  .form-check {
    position: relative;
    z-index: 0;
    display: block;
    user-select: none;

    // Checkbox
    .form-check-input[type="checkbox"] {
      position: absolute;
      left: -9999px;
      z-index: -1;
      appearance: none;
  
      & + label,
      & + .form-check-label {
        position: relative;
        padding: 1px 0;
        padding-left: $form-check-input-gutter;
        line-height: 2rem;
        cursor: pointer;
        @include transition(color .3s $transition-cubic);
            
        &::before {
          position: absolute;
          top: .45rem;
          left: 0;
          z-index: 1;
          content: "";
          border: $form-check-border-width solid $form-check-border-color;

          @if $enable-shadows {
            @include box-shadow($form-check-shadow);
          }
          @include square(1.32rem);
          @include transition(all .3s $transition-cubic);
        }
      }
    
      &:checked + label,
      &:indeterminate + label {
        &::before {
          height: .6rem;
          border-width: 2px;
          border-top-style: none;
          border-right-style: none;
          transform: rotate(-45deg);

          @include box-shadow(none);
          @include css-variable(border-color, $primary, --color-primary);
        }
      
      }

      &:not(:disabled):not(:checked) + label:hover,
      &:not(:disabled):not(:checked) + .form-check-label:hover {
        &::before {
          border-color: $form-check-hover-border-color;
        }
      }

      &:not(:checked):active + label::before,
      &:not(:checked):focus + label::before {
        @include form-simple-focus();
      }

      &:checked:active + label::before,
      &:checked:focus + label::before {
        border-width: 3px;
      }
    
      &:disabled {
        & + label {
          pointer-events: none;
          @include opacity(50);
        }
      
        & + label::before {
          pointer-events: none;
          @include opacity(50);
        }
      
        &:checked + label::after {
          pointer-events: none;
          @include opacity(50);
        }
      }
    }

    // Radio
    .form-check-input[type="radio"] {
      position: absolute;
      left: -9999px;
      z-index: -1;
      appearance: none;

      & + label,
      & + .form-check-label {
        position: relative;
        min-height: 2rem;
        padding: 1px 0;
        padding-left: 2rem;
        line-height: 2rem;
        cursor: pointer;
        @include transition(color .3s $transition-cubic);
            
        &::before {
          position: absolute;
          top: .45rem;
          left: 0;
          z-index: 1;
          cursor: pointer;
          content: "";
          border: $form-check-border-width solid $form-check-border-color;

          @if $enable-shadows {
            @include box-shadow($form-check-shadow);
          }
          @include square(1.32rem);
          @include border-radius(50%);
          @include transition(all .3s $transition-cubic);
        }

        &::after {
          position: absolute;
          top: .45rem;
          left: 0;
          cursor: pointer;
          content: "";
          background-color: $form-check-border-color;
          transform: scale(1);
          transform-origin: 50% 50%;
        
          @include square(1.32rem);
          @include opacity(0);
          @include border-radius(50%);
          @include transition(all .3s $transition-cubic);
        }
      }

      &:active + label::before,
      &:focus + label::before {
        @include form-simple-active-focus();
      }

      &:checked + label,
      &:checked + .form-check-label {
        &::before {
          @include css-variable(border-color, $primary, --color-primary);
        }

        &::after {
          transform: scale(.5);
          @include opacity(100);
          @include css-variable(background-color, $primary, --color-primary);
        }
      }

      &:not(:disabled):not(:checked) + label:hover,
      &:not(:disabled):not(:checked) + .form-check-label:hover {
        &::before {
          border-color: $form-check-hover-border-color;
        }
      }

      &:disabled {
        & + label {
          pointer-events: none;
          @include opacity(50);
        }
      
        & + label::before {
          pointer-events: none;
          @include opacity(50);
        }
      
        &:checked + label::after {
          pointer-events: none;
          @include opacity(50);
        }
      
      }

    }
  }


  //
  // Switch
  //

  .form-switch {
    position: relative;
    display: block;
    user-select: none;

    &.switch-io {
      .form-switch-slider::after {
        position: absolute;
        top: 0;
        left: .4rem;
        z-index: 1;
        width: 2.25rem;
        height: 1rem;
        font-size: .6rem;
        line-height: 1.7rem;
        color: $form-switch-color;
        text-align: center;
        letter-spacing: 1.3rem;
        content: "IO";
      }
    }

    &:hover {
      .form-switch-input:not(:disabled):not(:checked) + label .form-switch-slider {
        border-color: $form-switch-hover-color;

        &::before {
          background-color: $form-switch-hover-color;
        }
      }
    }

    .form-switch-input[type="checkbox"] {
      position: absolute;
      left: -9999px;
      z-index: -1;
      appearance: none;
      
      &:checked + label .form-switch-slider {
        @include css-variable(border-color, $primary, --color-primary);
        
        &::before {
          transform-origin: left top;
          @if ($enable-transitions) {
            animation: switch-move-on .4s $transition-cubic alternate;
            animation-fill-mode: both;
          } @else {
            transform: translate(.9rem, 0);
          }

          @include css-variable(background-color, $primary, --color-primary);
        }

        &::after {
          @include css-variable(color, $primary, --color-primary);
        }
      }

      &:disabled {
        & ~ .form-switch-label {
          pointer-events: none;
          @include opacity(50);
        }
      }

      &:focus + label .form-switch-slider,
      &:active + label .form-switch-slider {
        @include form-simple-focus();
      }

      &:checked:focus + label .form-switch-slider,
      &:checked:active + label .form-switch-slider {
        @include form-simple-active-focus();
      }
    }

    .form-switch-slider {
      position: absolute;
      width: 2.75rem;
      height: 1.75rem;
      margin-top: $form-switch-input-margin-y - .1rem;
      margin-left: -$form-switch-input-gutter - .5rem;
      cursor: pointer;
      border: 1px solid $form-switch-color;
    
      @if $enable-shadows {
        @include box-shadow($input-box-shadow);
      }
      @include border-radius(.875rem);
      @include transition(all .3s $transition-cubic);
      
      &::before {
        position: absolute;
        top: .2rem;
        left: .25rem;
        z-index: 2;
        width: 1.2rem;
        height: 1.2rem;
        content: "";
        background-color: $form-switch-color;
        transform-origin: right top;

        @if ($enable-transitions) {
          animation: switch-move-off .4s cubic-bezier(.25, .8, .25, 1) alternate;
          animation-fill-mode: both;
        } @else {
          transform: translate(0, 0);
        }
      
        @include border-radius(50%);
        @include transition(all .3s $transition-cubic);
      }
    }

    .form-switch-label {
      display: block;
      min-height: 2rem;
      padding-left: $form-switch-input-gutter + .5rem;
      margin-bottom: .5rem;
      line-height: 2rem;
      cursor: pointer;
    }
  }


  //
  // Range
  //

  .form-range {
    position: relative;
    display: flex;
    justify-content: center;

    input[type="range"].form-range-input {
      width: $form-range-track-width;
      padding: 0 .5rem;
      margin: $form-range-thumb-height/2 0;
      background: transparent;
      appearance: none;

      &:focus {
        outline: none;
      }

      &::-webkit-slider-runnable-track {
        @include range-track();
        content: "";
        background: $form-range-track-color;
        border: $form-range-track-border-width solid $form-range-track-border-color;

        @include border-radius($form-range-track-radius);
      }
    
      &::-webkit-slider-thumb {
        @include range-thumb();
        margin-top: ((-$form-range-track-border-width * 2 + $form-range-track-height) / 2) - ($form-range-thumb-height / 2);
        content: "";
        appearance: none;
      }

      &:focus::-webkit-slider-runnable-track {
        background: lighten($form-range-track-color, $form-range-contrast);

      }

      &::-moz-range-track {
        @include range-track();
        content: "";
        background: $form-range-track-color;
        border: $form-range-track-border-width solid $form-range-track-border-color;

        @include border-radius($form-range-track-radius);
      }
      &::-moz-range-thumb {
        @include range-thumb();
      }

      &::-ms-track {
        @include range-track();
        color: transparent;
        content: "";
        background: transparent;
        border-color: transparent;
        border-width: $form-range-thumb-width 0;
      }

      &::-ms-fill-lower {
        content: "";
        background: darken($form-range-track-color, $form-range-contrast);
        border: $form-range-track-border-width solid $form-range-track-border-color;

        @include border-radius($form-range-track-radius*2);
      }
      &::-ms-fill-upper {
        content: "";
        background: $form-range-track-color;
        border: $form-range-track-border-width solid $form-range-track-border-color;

        @include border-radius($form-range-track-radius*2);
      }
      &::-ms-thumb {
        @include range-thumb();
        content: "";
      }
      &:focus::-ms-fill-lower {
        background: $form-range-track-color;
      }
      &:focus::-ms-fill-upper {
        background: lighten($form-range-track-color, $form-range-contrast);
      }
    }
  }


  //
  // File
  //

  .form-file {
    @if $enable-css-variables {
      --input-bg: #{$form-file-bg};
      --input-border: #{$form-file-border-color};
      --input-color: #{$form-file-color};
    }

    @include dark-mode(&) {
      --input-bg: #{$gray-900};
      --input-border: #{$gray-700};
      --input-color: #{$white};
    }

    position: relative;
    z-index: 0;
    display: inline-block;
    width: 100%;
    height: $form-file-height;
    margin-bottom: 0;

    .form-file-input {
      position: relative;
      z-index: 2;
      width: 100%;
      height: $form-file-height;
      margin: 0;
      cursor: pointer;
      opacity: 0;

      &:focus ~ .form-file-label {
        @include css-variable(border-color, $input-focus-border-color, --color-primary);
        @include form-simple-active-focus();
      }

      &:disabled ~ .form-file-label {
        background-color: $form-file-disabled-bg;
      }

      @each $lang, $value in $form-file-text {
        &:lang(#{$lang}) ~ .form-file-label::after {
          content: $value;
        }
      }
    }

    .form-file-label {
      position: absolute;
      top: 0;
      right: 0;
      left: 0;
      z-index: 1;
      height: $form-file-height;
      padding: $form-file-padding-y $form-file-padding-x;
      line-height: $form-file-line-height;
      cursor: pointer;
      border-style: solid;
      border-width: $form-file-border-width;

      @include css-variable(color, $form-file-color, --input-color);
      @include css-variable(background-color, $form-file-bg, --input-bg);
      @include css-variable(border-color, $form-file-border-color, --input-border);
      @include border-radius($form-file-border-radius);
      @include box-shadow($form-file-box-shadow);

      &::after {
        @if $enable-css-variables {
          --file-btn-bg: #{$form-file-bg};
          --file-btn-color: #{$form-file-color};
        }

        @include dark-mode(&) {
          --file-btn-bg: #{$gray-900};
          --file-btn-color: #{$white};
        }

        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        z-index: 3;
        display: block;
        height: $form-file-height-inner;
        padding: $form-file-padding-y $form-file-padding-x;
        line-height: $form-file-line-height;
        content: "Browse";
        border-left: inherit;

        @include css-variable(color, $form-file-button-color, --file-btn-color);
        @include css-variable(background-color, $form-file-button-bg, --file-btn-bg);
        // @include css-variable(border-color, $form-file-border-color, --file-btn-border);
        @include gradient-bg($form-file-button-bg);
        @include border-radius(0 $form-file-border-radius $form-file-border-radius 0);
      }
    }
  }


  //
  // File drag&drop
  //

  .form-file-dd {
    @if $enable-css-variables {
      --input-border: #{$input-border-color};
    }

    @include dark-mode(&) {
      --input-border: #{$gray-700};
    }

    position: relative;
    display: block;
    width: 100%;
    min-width: 250px;
    padding: 40px 0;
    margin: 0;
    text-align: center;
    border-style: dashed;
    border-width: 2px;

    @include css-variable(border-color, $input-border-color, --input-border);
    @include border-radius($input-border-radius);
    
    input {
      position: absolute;
      top: 0;
      left: 0;
      z-index: 1;
      width: 100%;
      height: 100%;

      @include opacity(0);
    }

    .btn {
      z-index: 2;
    }
  }
  

  //
  // Animations
  //

  @keyframes switch-move-on {
    0% {
      transform: translate(0, 0) scale(1, 1);
    }
    10% {
      transform: translate(.2rem, 0) scale(1.2, 1);
    }
    100% {
      transform: translate(.9rem, 0) scale(1, 1);
    }
  }

  @keyframes switch-move-off {
    0% {
      transform: translate(.9rem, 0) scale(1, 1);
    }
    10% {
      transform: translate(.7rem, 0) scale(1.2, 1);
    }
    100% {
      transform: translate(0, 0) scale(1, 1);
    }
  }

}
