//
// Progress bars
// ---------------------------------------


// Progress bar
@keyframes progress-bar-stripes {
  from { background-position: $progress-height 0; }
  to { background-position: 0 0; }
}

.progress {
  @if $enable-css-variables {
    --progress-bar-color: #{$progress-bar-light-color};
    --progress-bg: #{$progress-light-bg};
  }

  @include dark-mode(&) {
    --progress-bar-color: #{$progress-bar-dark-color};
    --progress-bg: #{$progress-dark-bg};
  }

  display: flex;
  height: $progress-height;
  overflow: hidden; // force rounded corners by cropping it
  font-size: $progress-font-size;
  
  @include css-variable(background-color, $progress-light-bg, --progress-bg);
  @include border-radius($progress-border-radius);
  @include box-shadow($progress-box-shadow);
}

.progress-bar {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  white-space: nowrap;

  @include css-variable(color, $progress-bar-light-color, --progress-bar-color);
  @include css-variable(background-color, $primary, --color-primary);
  @include transition($progress-bar-transition);
}

.progress-bar-striped {
  @include gradient-striped();
  background-size: $progress-height $progress-height;
}

.progress-bar-animated {
  animation: progress-bar-stripes $progress-bar-animation-timing;
}

// Progress steps
.progress-steps {
  height: 55px;
  padding: 0;
  margin: 0;
  counter-reset: step;

  li {
    position: relative;
    float: left;
    font-size: $progress-font-size;
    text-align: center;
    text-transform: uppercase;
    list-style-type: none;

    &::before {
      @if $enable-css-variables {
        --progress-bg: #{$progress-light-bg};
      }

      @include dark-mode(&) {
        --progress-bg: #{$gray-950};
      }
      display: block;
      width: 42px;
      height: 42px;
      margin: -5px auto 10px auto;
      line-height: $progress-line-height;
      text-align: center;
      content: counter(step);
      counter-increment: step;
      border: 2px solid $progress-light-border-color;
      border-radius: 50%;

      @include css-variable(background-color, $progress-light-bg, --progress-bg);
    }

    &::after {
      position: absolute;
      top: 15px;
      left: -50%;
      z-index: -1;
      width: 100%;
      height: 2px;
      content: "";
      background-color: $progress-light-border-color;
    }

    &:first-child::after {
      content: none;
    }

    &.filled {
      color: $primary;
      color: var(--color-primary);

      &::before {
        color: $progress-bar-light-color;
        @include css-variable(border-color, $primary, --color-primary);
        @include css-variable(background-color, $primary, --color-primary);
      }

      & + li::after {
        @include css-variable(background-color, $primary, --color-primary);
      }
    }

    &.active {
      @include css-variable(color, $primary, --color-primary);

      &::before {
        @include css-variable(border-color, $primary, --color-primary);
        @include css-variable(color, $primary, --color-primary);
      }
    }
  }

  &.step-1 li {
    width: 100%;
  }
  &.step-2 li {
    width: 50%;
  }
  &.step-3 li {
    width: 29.99%;
  }
  &.step-4 li {
    width: 25%;
  }
  &.step-5 li {
    width: 20%;
  }
}


// Progress dots
@keyframes indicator-pumping {
  0%   { transform: scale(1); }
  50% { transform: scale(1.1); }
  100% { transform: scale(1); }
}

.progress-dots {
  position: relative;
  box-sizing: border-box;
  display: inline-block;
  width: auto;
  max-width: 1400px;
  margin: auto;
  margin-bottom: 10px;
  overflow: hidden;
  list-style: none;

  &::before {
    position: absolute;
    top: 45%;
    right: 0;
    left: 0;
    height: 5px;
    padding: 0;
    margin: 0;
    margin-left: -2.5px;
    content: " ";
    background-color: $white;
  }

  .progress-dots-indicator {
    position: relative;
    z-index: 100;
    display: inline-block;
    width: 12px;
    height: 12px;
    margin: 20px;
    font-size: 1.4em;
    line-height: 32px;
    color: $white;
    text-align: center;
    vertical-align: middle;
    background-color: $white;

    border: 6px solid $secondary;
    border-radius: 50%;

    &.active {
      width: 42px;
      height: 42px;
      margin: 0 20px;
      transform: translateZ(1px);

      animation: indicator-pumping 4s ease both infinite;
    }
  }
}
