// stylelint-disable declaration-no-important, selector-list-comma-newline-after

//
// Typography
// --------------------------------------------------

//
// Logic classes
//

%headings {
  margin-bottom: $headings-margin-bottom;
  transition: color .4s cubic-bezier(.25, .8, .25, 1);

  @include base-font-family();
  @include text-opacity($headings-color, $headings-opacity);
}


//
// Elements
//

// Headings
h1,
.h1 {
  @extend %headings;
  @include font-base($h1-font-size, $headings-font-weight, $headings-line-height, $headings-font-family);
}

h2,
.h2 {
  @extend %headings;
  @include font-base($h2-font-size, $headings-font-weight, $headings-line-height, $headings-font-family);
}

h3,
.h3 {
  @extend %headings;
  @include font-base($h3-font-size, $headings-font-weight, $headings-line-height, $headings-font-family);
}

h4,
.h4 {
  @extend %headings;
  @include font-base($h4-font-size, $headings-font-weight, $headings-line-height, $headings-font-family);
}

h5,
.h5 {
  @extend %headings;
  @include font-base($h5-font-size, $headings-font-weight, $headings-line-height, $headings-font-family);
}

h6,
.h6 {
  @extend %headings;
  @include font-base($h6-font-size, $headings-font-weight, $headings-line-height, $headings-font-family);
}


// Type display classes
.display-1 {
  @include font-base($display1-size-default, $display1-weight, $display-line-height);

  @each $breakpoint, $display1-size in $display1-sizes {
    @include media-breakpoint-up($breakpoint, $grid-breakpoints) {
      font-size: $display1-size;
    }
  }
}

.display-2 {
  @include font-base($display2-size-default, $display2-weight, $display-line-height);

  @each $breakpoint, $display2-size in $display2-sizes {
    @include media-breakpoint-up($breakpoint, $grid-breakpoints) {
      font-size: $display2-size;
    }
  }
}

.display-3 {
  @include font-base($display3-size-default, $display3-weight, $display-line-height);

  @each $breakpoint, $display3-size in $display3-sizes {
    @include media-breakpoint-up($breakpoint, $grid-breakpoints) {
      font-size: $display3-size;
    }
  }
}

.display-4 {
  @include font-base($display4-size-default, $display4-weight, $display-line-height);

  @each $breakpoint, $display4-size in $display4-sizes {
    @include media-breakpoint-up($breakpoint, $grid-breakpoints) {
      font-size: $display4-size;
    }
  }
}

.lead {
  font-size: $lead-font-size;
  font-weight: $lead-font-weight;
}


// Section headings
.section-title {
  padding-top: 0;
  padding-bottom: 40px;
}
  
.section-subtitle {
  padding-bottom: 40px;
  margin-top: -24px;
  word-spacing: .3px;
}
  
.section-description {
  margin-bottom: 16px;
  word-spacing: .3px;
}


//
// Horizontal rules
//

hr {
  margin-top: $hr-margin-y;
  margin-bottom: $hr-margin-y;
  border: 0;
  border-top: $hr-border-width solid $hr-border-color;
}


//
// Emphasis
//

small,
.small {
  @include font-base($small-font-size, $font-weight-normal, $line-height-base);
  @include text-opacity($small-color, $small-opacity);
}

mark,
.mark {
  padding: $mark-padding;
  background-color: $mark-bg;
}


//
// Lists
//

.list-unstyled {
  @include list-unstyled;
}

// Inline turns list items into inline-block
.list-inline {
  @include list-unstyled;
}
.list-inline-item {
  display: inline-block;

  &:not(:last-child) {
    margin-right: $list-inline-padding;
  }
}


//
// Misc
//

// Builds on `abbr`
.initialism {
  font-size: 90%;
  text-transform: uppercase;
}

// Blockquotes
.blockquote {
  margin-bottom: $spacer;
  font-size: $blockquote-font-size;
}

.blockquote-footer {
  display: block;
  font-size: 80%; // back to default font-size
  color: $blockquote-small-color;

  &::before {
    // stylelint-disable-next-line no-eol-whitespace
    // nbsp em dash
    content: "\2014 \00A0";
  }
}


//
// Effects
//

.title-underline {
  position: relative;

  &::after {
    position: absolute;
    bottom: 20px;
    left: 3px;
    width: 45px;
    content: "";
    border-bottom: 5px solid $primary;
    @include css-variable(border-color, $primary, --color-primary, !important);
  }

  &.text-center::after {
    left: 50%;
    transform: translate(-50%, 0);
  }

  &.text-right::after {
    right: 3px;
    left: auto;
  }
}
