// Base class
//
// Kickstart any navigation component with a set of style resets. Works with
// `<nav>`s or `<ul>`s.

.nav {
  display: flex;
  flex-wrap: wrap;
  padding-left: 0;
  margin-bottom: 0;
  list-style: none;
}

.nav-link {
  display: block;
  padding: $nav-link-padding-y $nav-link-padding-x;

  @include hover-focus {
    text-decoration: none;
  }

  // Disabled state lightens text
  &.disabled {
    color: $nav-link-disabled-color;
  }
}

//
// Tabs
//

.nav-tabs {
  border-bottom: $nav-tabs-border-width solid $nav-tabs-border-color;

  .nav-item {
    margin-bottom: -$nav-tabs-border-width;
  }

  .nav-link {
    border: $nav-tabs-border-width solid transparent;
    @include border-top-radius($nav-tabs-border-radius);

    @include hover-focus {
      background-color: $nav-tabs-link-hover-bg;
    }

    @include dark-mode(&) {
      @include hover-focus {
        background-color: $nav-tabs-link-hover-bg-dark;
      }

      &.disabled {
        background-color: transparent;
      }
    }

    &.disabled {
      color: $nav-link-disabled-color;
      background-color: transparent;
      border-color: transparent;
    }
  }

  .nav-link.active,
  .nav-item.show .nav-link {
    color: $nav-tabs-link-active-color;
    background-color: $nav-tabs-link-active-bg;
    @include css-variable(background-color, $nav-tabs-link-active-bg, --card-bg);
    border-color: $nav-tabs-link-active-border-color;

    @include dark-mode(&) {
      @include text-opacity($white, 1);
      @include css-variable(border-color, $gray-700, --card-bg);
      @include css-variable(background-color, $gray-700, --card-bg);
    }
  }

  .dropdown-menu {
    // Make dropdown border overlap tab border
    margin-top: -$nav-tabs-border-width;
    // Remove the top rounded corners here since there is a hard edge above the menu
    @include border-top-radius(0);
  }
}


//
// Pills
//

.nav-pills {
  .nav-link {
    padding: .3rem 1.2rem;
    @include border-radius($nav-pills-border-radius);
  }

  .nav-link.active,
  .show > .nav-link {
    color: $nav-pills-link-active-color;
    background-color: $nav-pills-link-active-bg;
  }
}


//
// Slices
//

.nav-slices {
  .nav-link {
    position: relative;
    padding: 16px;
    margin-top: -8px;
    margin-bottom: -8px;

    &::before {
      position: absolute;
      right: 0;
      bottom: 0;
      left: 0;
      display: block;
      width: 100%;
      content: "";
      border-bottom-style: solid;
      border-bottom-width: 3px;
      transform: scaleX(0);
      
      @include css-variable(border-bottom-color, $nav-slices-active-bg, --color-primary);
      @include transition(all .3s $transition-cubic);
    }

    @include hover-focus {
      &::before {
        transform: scaleX(.2);
      }
    }

    &.active {
      &::before {
        color: $nav-pills-link-active-color;
        transform: scaleX(1);
      }
    }
  }
}

.navbar.navbar-dark .nav-slices .nav-link::before,
.nav-slices .nav-link.text-white::before {
  border-bottom-color: $white;
}


//
// Justified variants
//

.nav-fill {
  .nav-item {
    flex: 1 1 auto;
    text-align: center;
  }
}

.nav-justified {
  .nav-item {
    flex-basis: 0;
    flex-grow: 1;
    text-align: center;
  }
}


// Tabbable tabs
//
// Hide tabbable panes to start, show them when `.active`

.tab-content {
  > .tab-pane {
    display: none;
  }
  > .active {
    display: block;
  }
}
