// Button variants
//
// Easily pump out default styles, as well as :hover, :focus, :active,
// and disabled options for all buttons

@mixin button-variant($background, $color, $var: null) {
  background-color: transparent;
  background-image: none;
  @include css-variable(color, $color, --color-#{$var});

  // Color prefix
  &::before {
    @include css-variable(background-color, $background, --color-#{$var});
  }

  &:hover {
    @include css-variable(color, $color, --color-#{$var});

    &::before {
      @include opacity(4);
    }
  }

  // Disabled comes first so active can properly restyle
  &.disabled,
  &:disabled {
    color: color-yiq($background);
    background-color: $background;
  }

  &:not(:disabled):not(.disabled):active,
  &:not(:disabled):not(.disabled).active,
  .show > &.dropdown-toggle {
    &::before {
      @include opacity(8);
    }
  }

  &:focus,
  &.focus {
    // Avoid using mixin so we can pass custom focus shadow properly
    @include css-variable(color, $color, --color-#{$var});

    &::before {
      @include opacity(4);
    }
  }

  .ripple-effect{
    @if $enable-transitions {
      @include opacity(40);
      @include css-variable(background-color, $background, --color-#{$var});

    } @else {
      display: none;
    }
  }
}

@mixin button-fill-variant($background, $border, $var: null, $hover-background: darken($background, 7.5%), $hover-border: darken($border, 7.5%), $active-background: darken($background, 12.5%), $active-border: darken($border, 12.5%)) {
  color: color-yiq($background);
  @include gradient-bg($background, $var);
  @include css-variable(border-color, $border, --color-#{$var});

  // Color prefix
  &::before {
    @include css-variable(background-color, $white, --color-white);
  }

  &.btn-raised {
    @include box-shadow($btn-box-shadow);
  }

  &:hover {
    color: color-yiq($hover-background);

    &::before {
      @include opacity(8);
    }
  }

  &.btn-raised:focus,
  &.btn-raised.focus {
    // Avoid using mixin so we can pass custom focus shadow properly
    @if $enable-shadows {
      box-shadow: $btn-box-shadow;
    } @else {
      box-shadow: 0 0 0 $btn-focus-width rgba($border, .5);
    }
  }

  // Disabled comes first so active can properly restyle
  &.disabled,
  &:disabled {
    color: color-yiq($background);
    background-color: $background;
    border-color: $border;
  }

  &:not(:disabled):not(.disabled):active,
  &:not(:disabled):not(.disabled).active,
  .show > &.dropdown-toggle {
    color: color-yiq($active-background);
    @if $enable-gradients {
      background-image: none; // Remove the gradient for the pressed/active state
    }

    &::before {
      @include opacity(16);
    }

    &.btn-raised:focus {
      // Avoid using mixin so we can pass custom focus shadow properly
      @if $enable-shadows {
        box-shadow: $btn-active-box-shadow;
      } @else {
        box-shadow: 0 0 0 $btn-focus-width rgba($border, .5);
      }
    }
  }

  &:focus,
  &.focus {
    // Avoid using mixin so we can pass custom focus shadow properly
    color: color-yiq($hover-background);

    &::before {
      @include opacity(16);
    }
  }

  .ripple-effect{
    @if $enable-transitions {
      background: rgba($white, .4);
    } @else {
      display: none;
    }
  }
}

@mixin button-outline-variant($color, $var: null, $color-hover: color-yiq($color), $active-background: $color, $active-border: $color) {
  background-color: transparent;
  background-image: none;
  border-color: $color;
  @include css-variable(color, $color, --color-#{$var});
  @include css-variable(border-color, $color, --color-#{$var});

  // Color prefix
  &::before {
    @include css-variable(background-color, $color, --color-#{$var});
  }

  &:hover {
    @include css-variable(color, $color, --color-#{$var});

    &::before {
      @include opacity(4);
    }
  }

  &:focus,
  &.focus {
    box-shadow: 0 0 0 $btn-focus-width rgba($color, .5);
    @if $enable-css-variables {
      @supports ( (--a: 0)) {
        box-shadow: 0 0 0 $btn-focus-width rgba(var(--rgb-#{$var}), .5);
      }
    }
  }

  &.disabled,
  &:disabled {
    color: $color;
    background-color: transparent;
  }

  &:not(:disabled):not(.disabled):active,
  &:not(:disabled):not(.disabled).active,
  .show > &.dropdown-toggle {
    border-color: $active-border;

    &::before {
      @include opacity(8);
    }

    &:focus,
    &.focus {
      // Avoid using mixin so we can pass custom focus shadow properly
      box-shadow: 0 0 0 $btn-focus-width rgba($color, .5);
      @if $enable-css-variables {
        @supports ( (--a: 0)) {
          box-shadow: 0 0 0 $btn-focus-width rgba(var(--rgb-#{$var}), .5);
        }
      }
    }
  }

  .ripple-effect{
    @if $enable-transitions {
      @include opacity(40);
      @include css-variable(background-color, $color, --color-#{$var});
      
    } @else {
      display: none;
    }
  }
}

// Button sizes
@mixin button-size($padding-y, $padding-x, $font-size, $line-height, $border-radius) {
  padding: $padding-y $padding-x;
  font-size: $font-size;
  line-height: $line-height;
  // Manually declare to provide an override to the browser default
  @if $enable-rounded {
    border-radius: $border-radius;
  } @else {
    border-radius: 0;
  }
}
