//
// Dropdown
// --------------------------------

// The dropdown wrapper (`<div>`)
.dropup,
.dropright,
.dropdown,
.dropleft {
  position: relative;
}

.dropdown-toggle {
  // Generate the caret automatically
  @include caret;
}

// The dropdown menu
.dropdown-menu {
  @if $enable-css-variables {
    --dropdown-bg: #{$dropdown-bg};
  }

  @include dark-mode(&) {
    --dropdown-bg: #{$gray-800};
  }

  position: absolute;
  top: 100%;
  left: 0;
  z-index: $zindex-dropdown;
  float: left;
  min-width: $dropdown-min-width;
  padding: $dropdown-padding-y 0;
  margin: $dropdown-spacer 0 0; // override default ul
  overflow: hidden;
  font-size: $font-size-base; // Redeclare because nesting can cause inheritance issues
  color: $body-color;
  text-align: left; // Ensures proper alignment if parent has it changed (e.g., modal footer)
  list-style: none;
  pointer-events: none;
  background-clip: padding-box;
  border: $dropdown-border-width solid $dropdown-border-color;

  @if ($enable-transitions) {
    opacity: 0;
    transform: scaleY(.6) scaleX(.9);
    transform-origin: 0 0;
  } @else {
    display: none; // none by default, but block on "open" of the menu
  }

  @include css-variable(background-color, $dropdown-bg, --dropdown-bg);
  @include transition(all .2s $transition-cubic);
  @include border-radius($dropdown-border-radius);

  @if ($enable-shadows) {
    @include box-shadow($dropdown-box-shadow);
  } @else {
    border: 1px solid $dropdown-border-color;
  }
}

.dropdown-menu-right {
  right: 0;
  left: auto;

  @if ($enable-transitions) {
    transform-origin: 100% 0;
  }
}

// Allow for dropdowns to go bottom up (aka, dropup-menu)
// Just add .dropup after the standard .dropdown class and you're set.
.dropup {
  .dropdown-menu {
    top: auto;
    bottom: 100%;
    margin-top: 0;
    margin-bottom: $dropdown-spacer;
  }

  .dropdown-toggle {
    @include caret(up);
  }
}

.dropright {
  .dropdown-menu {
    top: 0;
    right: auto;
    left: 100%;
    margin-top: 0;
    margin-left: $dropdown-spacer;
  }

  .dropdown-toggle {
    @include caret(right);
    &::after {
      vertical-align: 0;
    }
  }
}

.dropleft {
  .dropdown-menu {
    top: 0;
    right: 100%;
    left: auto;
    margin-top: 0;
    margin-right: $dropdown-spacer;
  }

  .dropdown-toggle {
    @include caret(left);
    &::before {
      vertical-align: 0;
    }
  }
}

// When enabled Popper.js, reset basic dropdown position
// stylelint-disable no-duplicate-selectors
.dropdown-menu {
  &[x-placement^="top"],
  &[x-placement^="right"],
  &[x-placement^="bottom"],
  &[x-placement^="left"] {
    right: auto;
    bottom: auto;
  }
}
// stylelint-enable no-duplicate-selectors

// Dividers (basically an `<hr>`) within the dropdown
.dropdown-divider {
  @include nav-divider($dropdown-divider-bg);
}

// Links, buttons, and more within the dropdown menu
//
// `<button>`-specific styles are denoted with `// For <button>s`
.dropdown-item {
  @if $enable-css-variables {
    --dropdown-link-color: #{$dropdown-link-color};
    --dropdown-link-hover-color: #{$dropdown-link-hover-color};
    --dropdown-link-hover-bg: #{$dropdown-link-hover-bg};
    --dropdown-link-active-color: #{$dropdown-link-active-color};
    --dropdown-link-active-bg: #{$dropdown-link-active-bg};
  }

  @include dark-mode(&) {
    --dropdown-link-color: #{$gray-100};
    --dropdown-link-hover-color: #{$white};
    --dropdown-link-hover-bg: #{$gray-700};
    --dropdown-link-active-color: #{$white};
    --dropdown-link-active-bg: #{$gray-700};
  }
  
  position: relative;
  display: block;
  width: 100%; // For `<button>`s
  padding: $dropdown-item-padding-y $dropdown-item-padding-x;
  overflow: hidden;
  clear: both;
  font-weight: $font-weight-normal;
  text-align: inherit; // For `<button>`s
  white-space: nowrap; // prevent links from randomly breaking onto new lines
  background-color: transparent; // For `<button>`s
  border: 0; // For `<button>`s

  @include css-variable(color, $dropdown-link-color, --dropdown-link-color);
  @include transition(all .3s $transition-cubic);

  @include hover-focus {
    text-decoration: none;
    @include css-variable(color, $dropdown-link-hover-color, --dropdown-link-hover-color);
    @include css-variable(background-color, $dropdown-link-hover-bg, --dropdown-link-hover-bg);
  }

  &.active,
  &:active {
    text-decoration: none;
    @include css-variable(color, $dropdown-link-active-color, --dropdown-link-active-color);
    @include css-variable(background-color, $dropdown-link-active-bg, --dropdown-link-active-bg);
  }

  &.disabled,
  &:disabled {
    color: $dropdown-link-disabled-color;
    background-color: transparent;
    // Remove CSS gradients if they're enabled
    @if $enable-gradients {
      background-image: none;
    }
  }
}

.dropdown-menu.show {
  @if ($enable-transitions) {
    pointer-events: auto;
    opacity: 1;
    transform: scaleY(1) scaleX(1);
  } @else {
    display: block;
  }
}

// Dropdown section headers
.dropdown-header {
  display: block;
  padding: $dropdown-padding-y $dropdown-item-padding-x;
  margin-bottom: 0; // for use with heading elements
  font-size: $font-size-sm;
  color: $dropdown-header-color;
  white-space: nowrap; // as with > li > a
}

// Dropdown text
.dropdown-item-text {
  display: block;
  padding: $dropdown-item-padding-y $dropdown-item-padding-x;
  color: $dropdown-link-color;
}
