// stylelint-disable declaration-no-important
@each $color, $value in $theme-colors {
  @include bg-variant(".bg-#{$color}", $value, $color);
}

@include dark-mode(".bg-light") {
  @include css-variable(background-color, $gray-900, --color-gray-dark);
}

@if $enable-gradients {
  @each $color, $value in $theme-colors {
    @include bg-gradient-variant(".bg-gradient-#{$color}", $value);
  }
}

.bg-white {
  @include css-variable(background-color, $white, --color-white);
  @include transition(background-color .4s $transition-cubic);

  @include dark-mode(&) {
    @include css-variable(background-color, $black, --color-black);
  }
}

.bg-transparent {
  background-color: transparent !important;
}
