//
// Timeline layout
// --------------------------------------------------


// Timeline panels
// ---------------------

.timeline {
  position: relative;
  display: block;
  padding: 0;
  margin: auto;
  list-style: none;

  @if $enable-timeline {
    @include media-breakpoint-up(md) {
      padding: $timeline-wrap-padding 0;
    }
  }

  @if $enable-timeline {
    &::before {
      @if $enable-css-variables {
        --timeline-color: rgba(0, 0, 0, .1);
      }
      @include dark-mode(&) {
        --timeline-color: rgba(255, 255, 255, .1);
      }

      position: absolute;
      top: 0;
      bottom: 0;
      left: 50%;
      display: none;
      width: $timeline-width;
      padding: 0;
      margin: 0;
      margin-left: -($timeline-width / 2);
      content: "";
      @include css-variable(background-color, rgba($black, .1), --timeline-color);
			
      @include media-breakpoint-up(md) {
        display: block;
      }
    }
  }

  & > li {
    position: relative;
    box-sizing: border-box;
    display: table;
    width: 100%;
    margin: $timeline-panel-gutter;

    &::before,
    &::after {
      display: table;
      clear: both;
      content: "";
    }

    & > div {
      display: block;
      width: 100%;
      margin: 10px 0;

      @if $enable-timeline {
        @include media-breakpoint-up(md) {
          display: table-cell;
          width: 50%;
          vertical-align: top;
        }
      }

      .timeline-panel {
        @if $enable-css-variables {
          --card-bg: #{$card-bg};
          --card-border: #{$card-border-color};
        }

        @include dark-mode(&) {
          --card-bg: #{$card-bg-dark};
          --card-border: #{$card-bg-dark};
        }

        position: relative;
        box-sizing: border-box;
        display: inline-block;
        width: auto;
        max-width: 70%;
        margin: 0 30px 0 auto;
        text-align: left;
        border-radius: 2px;

        @include css-variable(background-color, $card-bg, --card-bg);
        @include border-radius($border-radius);

        @if $enable-shadows {
          border: none;
          box-shadow: $box-shadow-sm;
        }
        @else {
          border: $card-border-width solid $card-border-color;
        }
				
        @if $enable-timeline {
          @include media-breakpoint-up(md) {
            float: right;
          }
          @include media-breakpoint-up(md) {
            max-width: 80%;
          }

          @include media-breakpoint-up(lg) {
            max-width: 90%;
          }
        }
        @else {
          @include media-breakpoint-up(md) {
            max-width: 50%;
          }
        }

        &::before {
          position: absolute;
          top: 11px;
          right: -15px;
          display: inline-block;
          content: "";
          border-top: 15px solid transparent;
          border-right: 0 solid $gray-300;
          border-bottom: 15px solid transparent;
          border-left: 15px solid $gray-300;
          @include css-variable(border-right-color, $card-border-color, --card-border);
          @include css-variable(border-left-color, $card-border-color, --card-border);
        }

        &::after {
          position: absolute;
          top: 12px;
          right: -14px;
          display: inline-block;
          content: "";
          border-top: 14px solid transparent;
          border-right: 0 solid $white;
          border-bottom: 14px solid transparent;
          border-left: 14px solid $white;

          @include css-variable(border-right-color, $card-bg, --card-bg);
          @include css-variable(border-left-color, $card-bg, --card-bg);
        }
      }

      &.timeline-column-left {
        text-align: right;
      }

      &.timeline-column-right {
        text-align: left;

        .timeline-panel {
          margin: 0 auto 0 30px;

          @if $enable-timeline {
            @include media-breakpoint-up(md) {
              float: left;
            }
          }

          &::before {
            right: auto;
            left: -15px;
            border-right-width: 15px;
            border-left-width: 0;
          }

          &::after {
            right: auto;
            left: -14px;
            border-right-width: 14px;
            border-left-width: 0;
          }
        }
      }

      @if $enable-timeline {
        .timeline-badge {
          position: absolute;
          top: 8px;
          left: 50%;
          z-index: 100;
          display: none;
          width: 32px;
          height: 32px;
          margin-left: -16px;
          font-size: 1.4em;
          line-height: 32px;
          color: $white;
          text-align: center;
          background-color: #999;
          border: 6px solid $gray-500;
					 
          @include border-radius(32px);
					 
          @include media-breakpoint-up(md) {
            display: block;
          }

          &.primary {
            background-color: #2e6da4;
          }
          &.success {
            background-color: #3f903f;
          }
          &.warning {
            background-color: #f0ad4e;
          }
          &.danger {
            background-color: #d9534f;
          }
          &.info {
            background-color: #5bc0de;
          }
        }
      }

      .timeline-body {
        padding: $timeline-panel-padding;
      }

    }

  }

}


// Timeline dates
// ----------------------
.timeline-date {
  position: relative;
  display: block;
  float: left;
  width: 50%;
  padding: 20px;
  padding: 0;
  padding-top: 10px;
  margin: 0 auto 0 40px;
  text-align: left;

  h1 {
    display: inline-block;
    padding: 0;
    margin: 0 3px;
    color: #999;
    letter-spacing: -.01em;
  }

  div {
    display: inline-block;
    height: 60px;

    p {
      padding: 0;
      margin: 0;
      font-size: 14px;
      line-height: 20px;
      color: #999;

      &:first-child {
        font-size: 24px;
        font-weight: 600;
        line-height: 30px;
      }
    }
  }
}
.timeline-inverted .timeline-date {
  float: right;
  margin: 0 40px 0 auto;
  text-align: right;
}


// Timeline label
// -------------------------

.timeline-label {
  position: relative;
  display: block;
  width: 100px;
  padding: 5px;
  margin: auto;
  font-weight: $font-weight-bold;
  color: $white;
  letter-spacing: 1.2px;
  background: #999;
	
  @include border-radius(3px);
}
